<template>
  <div class="list">
    <h2>駕駛資料</h2>
    <div class="seach">
        <el-input placeholder="輸入關鍵字" v-model="seach.key" prefix-icon="el-icon-search"></el-input>
	      <el-select v-model="seach.status" placeholder="請選擇個案類別">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
	      </el-select>
	      <el-select v-model="seach.compId" placeholder="請選擇公司">
          <el-option :label="'請選擇公司'" :value="''"></el-option>
          <el-option v-for="(item,index) in comp" :key="index" :label="item.CompanyName" :value="item.Id"></el-option>
	      </el-select>
        <el-button type="primary" icon="el-icon-search" @click="show_c">搜尋</el-button>
        <router-link :to="'/DriverMore/'"><el-button type="primary" icon="el-icon-circle-plus-outline" @click="show_c">新增</el-button></router-link>
        <a href="./download/%E5%8F%B8%E6%A9%9F%E8%BB%8A%E8%BC%9B%E5%8C%AF%E5%85%A5%E7%AF%84%E4%BE%8B.xlsx" download style="float:right;" :class="'import-bt'"><el-button type="primary">下載匯入範本</el-button></a>
        <el-upload :class="'import-bt'" :action="GLOBAL.up_xls" :show-file-list="false" :on-success="upload_xls_ok" :on-progress="upload_xls_loading" accept=".xls">
          <el-button type="primary">匯入</el-button>
        </el-upload>
    </div>
      <div class="list-d case-list list-title">
        <el-row>
          <el-col :xl="3" :md="7" :sm="7" :xs="7">照片</el-col>
          <el-col :xl="3" :md="5" :sm="5" :xs="5">姓名</el-col>
          <el-col :xl="3" class="hidden-md-and-down">身分證字號</el-col>
          <el-col :xl="3" :md="5" :sm="5" :xs="5">手機</el-col>
          <el-col :xl="3" class="hidden-md-and-down">駕照</el-col>
        </el-row>
      </div>
      <div class="list-d case-list" v-for="(item,index) in list.data">
        <el-row>
          <el-col :xl="3" :md="7" :sm="7" :xs="7"><img class="list-d-img" :src="item.Pic ? item.Pic : 'https://api.donkeymove.com//UploadFolder/images/default.png'"></el-col>
          <el-col :xl="3" :md="5" :sm="5" :xs="5">{{item.DriverName}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{Function.re_star(item.UID)}}</el-col>
          <el-col :xl="3" :md="5" :sm="5" :xs="5">{{item.Phone}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down" v-if="get_card(item).length!=0">{{get_card(item)[0].CarType.CarTypeName}}</el-col>
        </el-row>
        <div class="list-d-menu">
          <router-link :to="'/DriverMore/'+item.Id"><i class="el-icon-tickets" title="更多"></i></router-link>
          <i class="el-icon-unlock hidden-md-and-down" title="解鎖" @click="unlock(item)" v-if="item.PasswordLib&&item.PasswordLib.plErrorCount>=10&&GLOBAL.user.RoleId==34"></i>
          <i class="el-icon-delete hidden-md-and-down" title="刪除" @click="del(item)"></i>
        </div>
      </div>
	<div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'DriverList'" :q="seach"/>
  </div>
</template>

<script>
import Seach from './Seach'
import Page from './Page'
export default {
  data() {
    return {
	    list:[],
      options:[
        {value: "",label: "全部"},
        {value: "1",label: "可派發"},
        {value: "0",label: "不可派發"},
      ],
      comp:[],
      seach:this.Function.p_json(this.$route.query)
	  }
  },
  components: {Page,Seach},
  mounted:async function() {
    this.GLOBAL.show_black=true
    await this.all_ready()
    this.comp=await this.Function.get_CarCompanys()
    await this.get_data()
	  this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
	    this.GLOBAL.show_black=true
      await this.get_data()
	    this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	    this.list=await this.Function.get_driver(this.$route.params.page,encodeURI(this.seach.key),this.seach.status,this.seach.compId)
	    console.log(this.list)
    },
    show_c:async function() {
      console.log(this.$route)
      this.$router.push({ name:"DriverList" ,params:{page:1} ,query: this.seach})
    },
    del:async function(x) {
      var r = confirm("確定刪除?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.del_driver(x.Id))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
	  upload_xls_ok:async function(res) {
	    this.$message(res.msg)
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    upload_xls_loading:function() {
      this.GLOBAL.show_black=true
    },
    unlock:async function(x) {
      var r = confirm("確定解鎖?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.unlock_driver(x.PasswordLib.plAccountName))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    get_card:function(x) {
      var arr = x.DriverMapper.filter(re => re.CarType&&re.CarType.ParentID==9);
      return arr
    },
  }
}
</script>




