<template>
  <div class="list">
    <MBox :title="'新增管理員'">
      <div class="admin-list">
        <el-row>
          <el-col :span="8">管理員姓名<span class="red">(必填)</span></el-col>
          <el-col :span="16"><el-input v-model="c_list.CompanyUserName"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員角色<span class="red">(必填)</span></el-col>
          <el-col :span="16">
            <el-select v-model="c_list.RoleId" @change="clear_c()">
              <el-option v-for="(item,index) in Role.data" :label="item.Description" :value="item.Id"></el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員帳號<span class="red">(必填)</span></el-col>
          <el-col :span="16"><el-input v-model="c_list.Account"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員密碼</el-col>
          <el-col :span="16"><el-input v-model="c_list.Password"></el-input></el-col>
        </el-row>
        <el-row v-if="c_list.RoleId==34">
          <el-col :span="8"><span class="red">(必填)</span>管理員公司</el-col>
          <el-col :span="16">
            <el-select v-model="c_list.CompanyId">
              <el-option v-for="(item,index) in CarCompanys" :label="item.CompanyName" :value="item.Id"></el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row v-if="c_list.RoleId==33||c_list.RoleId==32">
          <el-col :span="8"><span class="red">(必填)</span>管理員A單位</el-col>
          <el-col :span="16">
            <el-select v-model="c_list.CompanyUserUnitMapping[0]">
              <el-option v-for="(item,index) in MainUnitA" :label="item.UnitName" :value="item.Id"></el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員編號</el-col>
          <el-col :span="16"><el-input v-model="c_list.CompanyUserNo"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員Email</el-col>
          <el-col :span="16"><el-input v-model="c_list.Email"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員手機</el-col>
          <el-col :span="16"><el-input v-model="c_list.Phone"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員市話</el-col>
          <el-col :span="16"><el-input v-model="c_list.Tel"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員性別</el-col>
          <el-col :span="16">
            <el-select v-model="c_list.Sex">
              <el-option :label="'男'" :value="'1'"></el-option>
              <el-option :label="'女'" :value="'2'"></el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">管理員身分證</el-col>
          <el-col :span="16"><el-input v-model="c_list.UID"></el-input></el-col>
        </el-row>
        <div class="bt" @click="sub()">確定提交</div>
      </div>
    </MBox>
    <h2>系統管理員</h2>
    <div class="seach">
        <el-input placeholder="輸入關鍵字" v-model="seach.key" prefix-icon="el-icon-search"></el-input>
      <el-button type="primary" icon="el-icon-search" @click="show_c">搜尋</el-button>
      <el-button type="primary" icon="el-icon-circle-plus-outline" @click="edit=false;c_list=empty;MBox.switch_open()">新增</el-button>
    </div>
    <div class="list-d case-list list-title">
      <el-row>
        <el-col :xl="3" :md="7" :sm="7" :xs="7">照片</el-col>
        <el-col :xl="7" :md="5" :sm="5" :xs="5">姓名</el-col>
        <el-col :xl="7" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">Email</el-col>
        <el-col :xl="7" :md="5" :sm="5" :xs="5">性別</el-col>
      </el-row>
    </div>
    <div class="list-d case-list" v-for="(item,index) in list.data">
      <el-row>
        <el-col :xl="3" :md="7" :sm="7" :xs="7"><img class="list-d-img" :src="item.Pic ? item.Pic : 'https://api.donkeymove.com//UploadFolder/images/default.png'"></el-col>
        <el-col :xl="7" :md="5" :sm="5" :xs="5">{{item.CompanyUserName}}</el-col>
        <el-col :xl="7" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">{{item.Email}}</el-col>
        <el-col :xl="7" :md="5" :sm="5" :xs="5">{{item.Sex==1 ? "男":"女"}}</el-col>
      </el-row>
      <div class="list-d-menu">
        <i class="el-icon-tickets" title="更多" @click="edit=true;c_list=item;MBox.switch_open()"></i>
        <i class="el-icon-unlock hidden-md-and-down" title="解鎖" @click="unlock(item)" v-if="item.PasswordLib&&item.PasswordLib.plErrorCount>=10&&(GLOBAL.user.RoleId==31||GLOBAL.user.RoleId==30)"></i>
        <i class="el-icon-document-delete hidden-md-and-down" title="停用" @click="UserEnable(item,false)" v-if="(GLOBAL.user.RoleId==30||GLOBAL.user.RoleId==31)&&item.Enabled"></i>
        <i class="el-icon-document-checked hidden-md-and-down" title="解除停用" @click="UserEnable(item,true)" v-if="(GLOBAL.user.RoleId==30||GLOBAL.user.RoleId==31)&&!item.Enabled"></i>
        <i class="el-icon-delete hidden-md-and-down" title="刪除" @click="del(item)"></i>
      </div>
    </div>
	<div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'AdminList'" :q="seach" />
  </div>
</template>

<script>
import Vue from 'vue'
import Seach from './Seach'
import Page from './Page'
import MBox from './MBox'

export default {
  data() {
    return {
      show_mbox:true,
      list:[],
      CarCompanys:[],
      MainUnitA:[],
      Role:[],
      c_list:"",
      edit:false,
      empty:{
        "Id":0,
        "CompanyId":"",
		    "CompanyUserName":"",
        "Pic": "",
        "CompanyUserNo": "",
        "Account": "",
        "Password": "",
        "UID": "",
        "Phone": "",
        "Sex": "",
        "Tel": "",
		    "Email":"",
		    "RoleId": "",
		    "CompanyUserUnitMapping":[]
      },
      seach:this.Function.p_json(this.$route.query)
	  }
  },
  components: {Page,Seach,MBox},
  mounted:async function() {
    this.GLOBAL.show_black=true
	  await this.all_ready()
    await this.get_data()
    this.CarCompanys=await this.Function.get_CarCompanys()
    this.MainUnitA=await this.Function.get_MainUnitA_All()
    this.Role=await this.Function.get_Role()
	  this.GLOBAL.show_black=false
    console.log(this.$route)
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
      this.seach=this.Function.p_json(this.$route.query)
      await this.get_data()
	    this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function() {
      this.list=await this.Function.get_CompanyUser(this.$route.params.page)
	    console.log(this.list)
	  },
    show_c:async function() {
      console.log(this.$route)
      this.$router.push({ name:"CaseList" ,params:{page:1} ,query: this.seach})
    },
    del:async function(x) {
      var r = confirm("確定刪除?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.del_comp_user(x.Id))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    unlock:async function(x) {
      var r = confirm("確定解鎖?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.unlock_comp_user(x.PasswordLib.plAccountName))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    UserEnable:async function(x,y) {
      var text=y ? "確定解除停用"+x.CompanyUserName+"帳號?" : "確定停用"+x.CompanyUserName+"帳號?"
      var r = confirm(text);
      if(!r) return 0
      this.GLOBAL.show_black=true
      alert(await this.Function.put_PutCompanyUserEnable(x.Id,y))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    clear_c:function() {
      
    },
  	check_input:function() {
	    if(!this.c_list.CompanyUserName) {
		    alert("姓名未填")
		    return false
	    }
	    if(!this.c_list.RoleId) {
		    alert("角色未選")
		    return false
      }
	    if(!this.c_list.Account) {
		    alert("帳號未填")
		    return false
      }
	    if(this.c_list.RoleId==34&&!this.c_list.CompanyId) {
		    alert("公司未選")
		    return false
      }
      if(this.c_list.RoleId!=34&&!this.c_list.CompanyUserUnitMapping[0]) {
		    alert("A單位未選")
		    return false
      }
      if(this.c_list.UID&&!this.Function.check.check_uid(this.c_list.UID)) {
        alert("身份證字號格式不符")
        return false
      }
      return true
	  },
    sub:async function() {
      for(let item of this.c_list.CompanyUserUnitMapping) {
        item={UnitId:item}
      }
      if(!this.check_input()) return 0 
      this.GLOBAL.show_black=true
      if(!this.edit) {
        alert(await this.Function.post_comp_user(this.c_list))
      }
      else {
        alert(await this.Function.put_comp_user(this.c_list))
      }
      await this.get_data()
      this.GLOBAL.show_black=false
    }
  }
}
</script>



