<template>
  <div class="list">
    <h2>A單位一覽表</h2>
    <div class="seach">
      <el-button type="primary" icon="el-icon-search" @click="is_edit=false;c_list=empty;MBox.switch_open()">新增</el-button>
    </div>
      <div class="list-d comp-list" v-for="(item,index) in list.data">
        <div><span>{{item.UnitName}}</span></div><hr>
        <div class="comp-list-d">
          <div><span>統一編號:</span>{{item.EINNO}}</div>
          <div><span>聯絡人:</span>{{item.ContactName}}</div>
          <div><span>聯絡人手機:</span>{{item.Phone}}</div>
        </div>
        <div class="list-d-menu">
          <i class="el-icon-tickets" title="更多" @click="is_edit=true;c_list=item;MBox.switch_open()"></i>
        </div>
      </div>
	<div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'ACompanyList'" :q="seach"/>
  <MBox :title="'新增營運單位'">
    <div class="admin-list">
      <el-row>
        <el-col :span="8">名稱</el-col>
        <el-col :span="16"><el-input v-model="c_list.UnitName"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">統一編號</el-col>
        <el-col :span="16"><el-input v-model="c_list.EINNO"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">聯絡人</el-col>
        <el-col :span="16"><el-input v-model="c_list.ContactName"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">手機</el-col>
        <el-col :span="16"><el-input v-model="c_list.Phone"></el-input></el-col>
      </el-row>
      <div class="bt" @click="sub()">確定提交</div>
    </div>
  </MBox>
  </div>
</template>

<script>
import Page from './Page'
import MBox from './MBox'
export default {
  data() {
    return {
      list:[],
      c_list:"",
      is_edit:false,
      empty:{
        "UnitName": "",
        "Phone": "",
        "EINNO": "",
        "ContactName": "",
      },
      seach:this.Function.p_json(this.$route.query)
	  }
  },
  components: {Page,MBox},
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data()
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
    await this.get_data()
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function() {
	    this.list=await this.Function.get_MainUnitA(this.$route.params.page)
	    console.log(this.list)
    },
    sub:async function() {
      this.GLOBAL.show_black=true
      if(this.is_edit) {
        alert(await this.Function.put_MainUnitA(this.c_list))
      }
      else {
        alert(await this.Function.post_MainUnitA(this.c_list))
      }
      await this.get_data()
      this.GLOBAL.show_black=false
    }
  }
}
</script>




