<template>
  <div class="stroke" :style="{'height':get_hei()+'px'}">
    <div class="stroke-time">
      <div class="stroke-time-d" v-for="(item,index) in get_time_arr()">{{item}}</div>
    </div>
    <div class="stroke-d" :style="{'height':get_hei()+'px'}">
      <div class="stroke-d-d" v-for="(item,index) in order" @dblclick="mbox(true,item.DespatchDetails[0])" :style="{'height':(get_hei()/get_min())*item.DespatchDetails[0].OrderDetails.ExpectedMinute+'px','top':(get_hei()/get_min())*get_ex(item)+'px'}">
	    <div class="stroke-d-d-more">{{item.DespatchDetails[0].OrderDetails.SOrderNo}}</div>
      </div>
    </div>	   
  </div>
</template>

<script>
import moment from 'moment'
export default {
    props: ['order','mbox'],
    data: function() {
      return {
	    time:[5,24],
      }
    },
	mounted:function() {
	  for(let i=0;i<document.querySelectorAll(".d1-d").length;i++) {
	    arr.push(document.querySelectorAll(".d1-d")[i])
	  }
	},
	methods: {
	  get_hei:function() {
	    return (this.time[1]-this.time[0])*100;
	  },
	  get_min:function() {
	    return (this.time[1]-this.time[0])*60;
	  },
	  get_ex:function(x) {
	    var arr=moment(x.DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm").split(":")
	    return ((arr[0]-this.time[0])*60)+Number(arr[1]);
	  },
	  get_order_min:function(x) {
	    var arr_time=[x.time]
	    var arr=moment(x.DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm").split(":")
		for(let i=0;i<x.ex;i++) {
		  arr[1]++
		  if(arr[1]>=60) {
		    arr[0]++
			arr[1]=0
		  }
		  arr_time.push(arr[0]+":"+arr[1])
		}
		return arr_time
      },
      get_time_arr:function() {
        var arr=[]
        var num=""
        for(let i=this.time[0];i<=this.time[1];i++) {
          num=i+":00"
          arr.push(num)
        }
        return arr
      }
    }
}
</script>


