<template>
  <div class="more">
    <h2>個案詳細資料</h2>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false" v-if="$route.params.id">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">{{$route.params.id ? "編輯":"新增"}}</div>
	  </div>
	  <div class="more-c">
	    <img :src="list.Pic" class="avatar" v-if="!edit">
        <el-upload class="avatar-uploader" :action="GLOBAL.up_img" :show-file-list="false" :on-success="upload_img_ok" v-if="edit">
          <img v-if="list.Pic" :src="list.Pic" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
		<el-button type="primary" icon="el-icon-document-checked" @click="sub()" v-if="edit">存檔</el-button>
        <div class="more-d">
		  <span>可否派發: {{list.Status ? "可派發":"不可派發"}}</span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-select v-model="list.Status">
			  <el-option :label="'可派發'" :value="1"></el-option>
			  <el-option :label="'不可派發'" :value="0"></el-option>
			</el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>個案類別: {{list.UserMode===1 ? "公費個案" : list.UserMode===2 ? "結案":"自費個案"}}</span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-select v-model="list.UserMode">
			  <el-option :label="'公費個案'" :value="1"></el-option>
			  <el-option :label="'自費個案'" :value="0"></el-option>
			  <el-option :label="'結案'" :value="2"></el-option>
			</el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d" v-if="list.UserMode==2">
		  <span>結案理由: {{list.CaseCloseRemark}}</span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-select v-model="list.CaseCloseRemark" @change="dead_res=''">
			  <el-option :label="'失能等級不符'" :value="'失能等級不符'"></el-option>
			  <el-option :label="'死亡'" :value="'死亡'"></el-option>
			  <el-option :label="'移出'" :value="'移出'"></el-option>
			  <el-option :label="'其他'" :value="'其他'"></el-option>
			</el-select>
			<el-input placeholder="結案理由" v-model="dead_res" v-if="list.CaseCloseRemark=='其他'"></el-input>
		  </div>
		  <hr>
		</div>
		<div class="more-d">
		  <span>個案編號: <span class="red" v-if="edit">(必填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{list.CaseUserNo}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-input placeholder="請輸入個案編號" v-model="list.CaseUserNo"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>個案姓名: <span class="red" v-if="edit">(必填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{list.Name}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-input placeholder="請輸入個案姓名" v-model="list.Name"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>性別: <span v-if="!edit||GLOBAL.user.RoleId==34">{{list.Sex==1 ? "男" : "女"}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-select v-model="list.Sex" placeholder="請選擇">
			  <el-option :label="'男'" value=1>男</el-option>
			  <el-option :label="'女'" value=2>女</el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>生日: <span class="red" v-if="edit">(必填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{Function.moment.yyyymmdd(list.Birthday)}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
			<el-date-picker v-model="list.Birthday" type="date" placeholder="選擇日期"></el-date-picker>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>身分證字號: <span class="red" v-if="edit">(必填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{list.UID}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-input placeholder="" v-model="list.UID"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>電子郵件: <span v-if="!edit||GLOBAL.user.RoleId==34">{{list.Email}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-input placeholder="請輸入電子郵件" v-model="list.Email"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>手機: <span class="red" v-if="edit">(手機市話擇一填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{list.Phone}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-input placeholder="請輸入手機" v-model="list.Phone"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>市話: <span class="red" v-if="edit">(手機市話擇一填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{list.Tel}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
		    <el-input placeholder="請輸入市話" v-model="list.Tel"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>社會福利身分: <span class="red" v-if="edit">(必填)</span><span v-if="list.UserType_name">{{list.UserType_name.TypeName}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-select v-model="list.UserType" placeholder="請選擇">
              <el-option v-for="(item,index) in list_1.data" :key="index" :label="item.TypeName" :value="item.Id"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>失能等級: <span class="red" v-if="edit">(必填)</span><span v-if="list.DisabilityId_name">{{list.DisabilityId_name.TypeName}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-select v-model="list.DisabilityId" placeholder="請選擇">
              <el-option v-for="(item,index) in list_3.data" :key="index" :label="item.TypeName" :value="item.Id"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>緯度: <span v-if="!edit">{{list.lat}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入緯度" v-model="list.lat"></el-input>
		  </div>
		  <hr>
		</div>	
        <div class="more-d">
		  <span>經度: <span v-if="!edit">{{list.lon}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入經度" v-model="list.lon"></el-input>
		  </div>
		  <hr>
		</div>		
        <div class="more-d">
		  <span>戶籍地址: <span class="red" v-if="edit">(必填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{list.CensusCounty+" "+list.CensusDistrict+" "+list.CensusAddr}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-select v-model="list.CensusCounty" placeholder="請選擇縣市">
              <el-option v-for="(item,index) in CitySel.city_sel.city_no" :key="index" :label="item" :value="item"></el-option>
            </el-select>
		    <el-select v-model="list.CensusDistrict" placeholder="請選擇地區">
              <el-option v-for="(item,index) in CitySel.city_sel[list.CensusCounty]" :key="index" :label="item" :value="item"></el-option>
            </el-select>
		    <el-input v-model="list.CensusAddr"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>通訊地址: <span class="red" v-if="edit">(必填)</span><span v-if="!edit||GLOBAL.user.RoleId==34">{{list.CommCounty+" "+list.CommDistrict+" "+list.CommAddr}}</span><span class="red" v-if="edit">(通訊地址若有異動，請同時更新經緯度)</span></span>
		  <div v-if="edit">
            <el-select v-model="list.CommCounty" placeholder="請選擇縣市">
              <el-option v-for="(item,index) in CitySel.city_sel.city_no" :key="index" :label="item" :value="item"></el-option>
            </el-select>
		    <el-select v-model="list.CommDistrict" placeholder="請選擇地區">
              <el-option v-for="(item,index) in CitySel.city_sel[list.CommCounty]" :key="index" :label="item" :value="item"></el-option>
            </el-select>
		    <el-input v-model="list.CommAddr"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>緊急聯絡人姓名: <span v-if="!edit||GLOBAL.user.RoleId==34">{{list.UrgentName}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-input placeholder="請輸入緊急聯絡人姓名" v-model="list.UrgentName"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>緊急聯絡人關係: <span v-if="!edit||GLOBAL.user.RoleId==34">{{list.UrgentRelationship}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-input placeholder="請輸入緊急聯絡人關係" v-model="list.UrgentRelationship"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>緊急聯絡人信箱: <span v-if="!edit||GLOBAL.user.RoleId==34">{{list.UrgentEmail}}</span></span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-input placeholder="請輸入緊急聯絡人信箱" v-model="list.UrgentEmail"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>使用額度: <span>{{Math.abs(list.CaseUserCouponViewModel.UseCouponAmt)}}</span></span>
		  <hr>
		</div>
        <div class="more-d">
		  <span>剩餘餘額: <span>{{Math.abs(list.CaseUserCouponViewModel.LastCouponAmt)}}</span></span>
		  <hr>
		</div>
        <div class="more-d">
		  <span>本月可用額度: <span>{{Math.abs(list.CaseUserCouponViewModel.TotalCouponAmt)}}</span></span>
		  <hr>
		</div>
        <div class="more-d" v-if="edit">
		  <span>新增額度: </span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-input-number v-model="add_c"></el-input-number>
		  </div>
		  <hr>
		</div>
        <div class="more-d" v-if="edit">
		  <span>特約機構: </span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-select value-key="CompanyId" placeholder="請選擇" v-model="list.CaseUserCompanyMapper" multiple>
              <el-option v-for="(item,index) in comp" :key="index" :label="item.CompanyName" :value="item"></el-option>
			</el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d" v-if="edit">
		  <span>A單位: </span>
		  <div class="more-d-edit" v-if="edit&&GLOBAL.user.RoleId!=34">
            <el-select placeholder="請選擇" v-model="list.UnitId">
              <el-option v-for="(item,index) in Acomp" :label="item.UnitName" :value="item.Id"></el-option>
			</el-select>
		  </div>
		  <hr>
		</div>
		<div class="more-c-d" v-if="!edit">
          <div class="more-c-d-h">違規紀錄</div>
		  <div class="more-c-d-c">
            <el-table :data="list.OrderViolationPoint.OrderViolation" style="width: 100%" v-if="list.OrderViolationPoint&&list.OrderViolationPoint.OrderViolation">
              <el-table-column label="訂單編號" style="width:calc(100% / 4);">
			    <template slot-scope="scope">
                  {{scope.row.OrderDetails.SOrderNo}}
                </template>
			  </el-table-column>
              <el-table-column label="點數" style="width:calc(100% / 4);">
			    <template slot-scope="scope">
                  {{scope.row.Point}}
                </template>
			  </el-table-column>
              <el-table-column label="備註" style="width:calc(100% / 4);">
			    <template slot-scope="scope">
                  {{scope.row.Remark}}
                </template>
			  </el-table-column>
              <el-table-column label="建立時間" style="width:calc(100% / 4);">
			    <template slot-scope="scope">
                  {{scope.row.CreateTime ? Function.moment.yyyymmdd(scope.row.CreateTime) : "無"}}
                </template>
			  </el-table-column>
            </el-table>	
		  </div>
		</div>
		<div class="more-c-d" v-if="!edit">
          <div class="more-c-d-h" @click="get_detail()">搭乘紀錄</div>
		  <div class="more-c-d-c" v-if="Detail">
            <el-table :data="Detail" style="width: 100%" v-if="Detail">
              <el-table-column label="訂單編號" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.SOrderNo}}
                </template>
			  </el-table-column>
              <el-table-column label="金額" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.SelfPayAmt}}
                </template>
			  </el-table-column>
              <el-table-column label="車號" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.CarNo}}
                </template>
			  </el-table-column>
              <el-table-column label="司機" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.DriverName}}
                </template>
			  </el-table-column>
              <el-table-column label="狀態" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.Status}}
                </template>
			  </el-table-column>
            </el-table>	
		  </div>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:{
            "Id": 0,
            "ContactUserId": "",
            "CaseUserNo": "",
            "UserType": 0,
            "CaseUserCouponViewModel": {
              "TotalCouponCount": 8,
              "UseCouponCount": 0,
              "LastCouponCount": 0,
              "MissBoarding": 0,
              "TotalCouponAmt": 1840,
              "UseCouponAmt": 0,
              "LastCouponAmt": 0,
              "CreateTime": "2019-11-20T07:32:35.358Z",
              "GrantMode": 0
            },
            "DisabilityId": "",
            "Name": "",
            "Pic": "",
            "CompanyId": "",
            "Sex": "",
            "IdentityStatus": "",
            "UID": "",
            "Tel": "",
            "Phone": "",
            "Email": "",
            "CensusCounty": "",
            "CensusDistrict": "",
            "CensusAddr": "",
            "CommCounty": "",
            "CommDistrict": "",
            "CommAddr": "",
            "UrgentName": "",
            "UrgentRelationship": "",
            "UrgentPhone": "",
            "UrgentTel": "",
            "UrgentEmail": "",
            "UnitId": "",
            "UserType": "",
            "ApplySubsidy": true,
            "Staus": 1,
            "UserMode": 1,
            "ReviewDate": "",
            "ExpiredDate": "",
            "Remark": "",
            "IsDeleted": true,
            "Enabled": false,
            "lat":"",
            "lon":"",
            "CaseCloseRemark":""

		  },
	  dead_res:"",
	  list_key:[],
	  edit:false,
	  list_30:[],  //身分別
	  list_30_text:[],
	  list_1:[],  //經濟狀況
	  list_1_text:[],
	  list_3:[],  //失能等級
	  list_3_text:[],
	  list_4:[],  //核定服務
	  list_4_text:[],
	  comp:[],
	  Acomp:[],
	  Detail:[],
	  Detail_num:0,
	  add_c:0,
	}
  },
  mounted:async function() {
	this.GLOBAL.show_black=true
	if(!this.$route.params.id) this.edit=true
	await this.all_ready()
	await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.comp=await this.Function.get_CarCompanys()
	  this.comp.forEach(x=>x.CompanyId=x.Id)
	  this.Acomp=await this.Function.get_MainUnitA_All()
	  if(x) this.list=await this.Function.get_case_more_edit(x)
	  this.list_30=await this.Function.get_parentID(30)
	  this.list_1=await this.Function.get_parentID(1)
	  this.list_3=await this.Function.get_parentID(3)
	  this.list.UserType_name=this.list_1.data.filter(x=>x.Id==this.list.UserType)[0]
	  this.list.DisabilityId_name=this.list_3.data.filter(x=>x.Id==this.list.DisabilityId)[0]
	  this.$forceUpdate()
	  console.log("list1")
	  console.log(this.list)
	  console.log(this.comp)
	  console.log(this.list.CaseUserCompanyMapper)
	},
    get_detail:async function() {
	  this.GLOBAL.show_black=true
	  this.Detail_num+=5
	  this.Detail=await this.Function.get_case_OrderDetails(this.$route.params.id,this.Detail_num)
	  console.log(this.Detail) 
	  this.$forceUpdate()
	  this.GLOBAL.show_black=false
	},
	upload_img_ok:function(res) {
	  this.$message(res.msg)
	  this.list.Pic=this.GLOBAL.call_img+res.response
	},
	check_input:function() {
	  if(this.GLOBAL.user.RoleId==34) {
        return true
	  }
	  if(!this.list.CaseUserNo) {
		alert("個案編號未填")
		return false
	  }
	  if(!this.list.Name) {
		alert("姓名未填")
		return false
	  }
	  if(!this.list.UserType) {
		alert("社會福利身分未填")
		return false
	  }
	  if(!this.list.DisabilityId) {
		alert("失能等級未填")
		return false
	  }
	  if(!this.list.Tel&&!this.list.Phone) {
		alert("手機跟市話至少擇一填")
		return false
	  }
	  if(!this.list.UID||!this.Function.check.check_uid(this.list.UID)) {
		alert("身分證字號未填或身分證字號格式不符")
		return false
	  }
	  if(!this.list.Birthday) {
		alert("生日未填")
		return false
	  }
	  if(!this.list.CensusCounty||!this.list.CensusDistrict||!this.list.CensusAddr) {
		alert("戶籍地址未填完整")
		return false
	  }
	  if(!this.list.CommCounty||!this.list.CommDistrict||!this.list.CommAddr) {
		alert("通訊地址未填完整")
		return false
	  }
	  if(this.list.UserMode==2&&!this.list.CaseCloseRemark) {
		alert("結案原因未填")
		return false
	  }
	  if(this.list.UserMode==2&&this.list.CaseCloseRemark=='其他'&&!this.dead_res) {
		alert("結案原因未填")
		return false
	  }
      return true
	},
	sort_data:function() {

	},
	sub:async function() {
	  if(!this.check_input()) return false
	  this.GLOBAL.show_black=true
	  if(this.dead_res) this.list.CaseCloseRemark=this.dead_res
	  if(!this.$route.params.id) {
        this.$message(await this.Function.post_case(this.list))
	  }
	  else {
        this.$message(await this.Function.put_case(this.list))
	  }
	  if(this.GLOBAL.user.RoleId!=34&&this.$route.params.id) this.$message(await this.Function.put_PutCoupon(this.$route.params.id,this.add_c))
	  history.go(-1)
	  this.GLOBAL.show_black=false
	}
  }
}
</script>



