<template>
  <div class="list">
    <h2>個案管理</h2>
    <MBox :title="GLOBAL.user.RoleId==34 ? '衛福部核銷檔匯出':'個案使用額度匯出'" ref="mb1">
      <div style="width:500px;">
        <el-date-picker v-model="s_time" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd"></el-date-picker>
        <el-date-picker v-model="e_time" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd"></el-date-picker>
        <downloadExcel :fetch="export_table" type="xls" :name="GLOBAL.user.RoleId==34 ? '衛福部核銷檔匯出.xls':'個案使用額度匯出.xls'"><div class="bt">確定提交</div></downloadExcel>
      </div>
    </MBox>
    <div class="seach">
        <el-input placeholder="輸入關鍵字" v-model="seach.key" prefix-icon="el-icon-search"></el-input>
	      <el-select v-model="seach.pMode" placeholder="請選擇個案類別">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
	      </el-select>
        <el-button type="primary" icon="el-icon-search" @click="show_c">搜尋</el-button>
      <router-link :to="'/CaseMore/'" v-if="GLOBAL.user.RoleId!=34"><el-button type="primary" icon="el-icon-circle-plus-outline" @click="show_c">新增</el-button></router-link>
      <el-button type="primary" icon="el-icon-circle-plus-outline" @click="$refs.mb1.switch_open()">{{GLOBAL.user.RoleId==34 ? "衛福部核銷檔匯出":"個案使用額度匯出"}}</el-button>
    </div>
      <div class="list-d case-list list-title">
        <el-row>
          <el-col :xl="3" :md="7" :sm="7" :xs="7">照片</el-col>
          <el-col :xl="3" :md="5" :sm="5" :xs="7">姓名</el-col>
          <el-col :xl="3" class="hidden-md-and-down">個案編號</el-col>
          <el-col :xl="3" class="hidden-md-and-down">身分證字號</el-col>
          <el-col :xl="3" class="hidden-md-and-down">生日</el-col>
          <el-col :xl="3" :md="3" :sm="3" :xs="3">性別</el-col>
          <el-col :xl="3" :md="7" :sm="7" :xs="7">手機</el-col>
          <el-col :xl="3" class="hidden-md-and-down">市話</el-col>
        </el-row>
      </div>
      <div class="list-d case-list" v-for="(item,index) in list.data">
        <el-row>
          <el-col :xl="3" :md="7" :sm="7" :xs="7"><img class="list-d-img" :src="item.Pic ? item.Pic : 'https://api.donkeymove.com//UploadFolder/images/default.png'"></el-col>
          <el-col :xl="3" :md="5" :sm="5" :xs="7">{{item.Name}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{item.CaseUserNo}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{Function.re_star(item.UID)}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{Function.moment.yyyymmdd(item.Birthday)}}</el-col>
          <el-col :xl="3" :md="3" :sm="3" :xs="3">{{item.Sex==1 ? "男" : "女"}}</el-col>
          <el-col :xl="3" :md="7" :sm="7" :xs="7">{{item.Phone}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{item.Tel}}</el-col>
        </el-row>
        <div class="list-d-menu">
          <i class="el-icon-truck" title="訂車" @click="to_re(item)"></i>
          <router-link :to="'/CaseMore/'+item.Id"><i class="el-icon-tickets" title="更多"></i></router-link>
          <i class="el-icon-unlock hidden-md-and-down" title="解鎖" @click="unlock(item)" v-if="item.ContactUser.PasswordLib&&item.ContactUser.PasswordLib.plErrorCount>=10&&GLOBAL.user.RoleId!=34"></i>
          <i class="el-icon-delete hidden-md-and-down" title="刪除" @click="del(item)" v-if="GLOBAL.user.RoleId!=34"></i>
        </div>
      </div>
	<div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'CaseList'" :q="seach" />
  </div>
</template>

<script>
import Vue from 'vue'
import Seach from './Seach'
import Page from './Page'
import MBox from './MBox'
import JsonExcel from 'vue-json-excel'
Vue.component('downloadExcel', JsonExcel)

export default {
  data() {
    return {
	    list:[],
      options:[
        {value: "1",label: "全部個案"},
        {value: "3",label: "自費個案"},
        {value: "4",label: "公費個案"},
        {value: "5",label: "結案"},
      ],
      s1:"",
      s2:1,
      s_time:"",
      e_time:"",
      seach:this.Function.p_json(this.$route.query)
	  }
  },
  components: {Page,Seach,MBox},
  mounted:async function() {
    this.GLOBAL.show_black=true
	  await this.all_ready()
    await this.get_data()
	  this.GLOBAL.show_black=false
    console.log(this.$route)
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
      this.seach=this.Function.p_json(this.$route.query)
      this.seach=to.query
      await this.get_data()
	    this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function() {
	    this.list=await this.Function.get_case_user(this.$route.params.page,encodeURI(this.seach.key),this.seach.pMode)
      console.log(this.list)
	  },
    show_c:async function() {
      console.log(this.$route)
      this.$router.push({ name:"CaseList" ,params:{page:1} ,query: this.seach})
    },
    del:async function(x) {
      var r = confirm("確定刪除?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.del_case_user(x.Id))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    unlock:async function(x) {
      var r = confirm("確定解鎖?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.unlock_case_user(x.ContactUser.ContactUID))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    export_table:async function() {
      var obj=""
      this.GLOBAL.show_black=true
      if(this.GLOBAL.user.RoleId==34) {
        obj=await this.Function.export_ExportExcel(this.s_time,this.e_time)
      }
      else {
        obj=await this.Function.export_ExportExcelCaseUser(this.s_time,this.e_time)
      }
      if(!obj) {
        this.GLOBAL.show_black=false
        alert("查無資料")
        return 0
      }
      this.Function.export.convertAndExport(obj)
      this.GLOBAL.show_black=false
      return obj
    },
    to_re:async function(x) {
      this.GLOBAL.show_black=true
      localStorage.dk2_admain=JSON.stringify(await this.Function.get_case_more_edit(x.Id))
      location.href="https://ntpc.donkeymove.com/NewTaipeiWeb1/customer/#/reservation"
    }
  }
}
</script>



