// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import "@babel/polyfill";
import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'
import Top from './Top'
import Left from './Left'
import List from './List'
import Global from './components/Global'
import Black from './components/Black'
import CarList from './components/CarList'
import CaseList from './components/CaseList'
import DriverList from './components/DriverList'
import CaseMore from './components/CaseMore'
import CarMore from './components/CarMore'
import DriverMore from './components/DriverMore'
import DragDispatch from './components/DragDispatch'
import TableDispatch from './components/TableDispatch'
import OrderList from './components/OrderList'
import OrderDetails from './components/OrderDetails'
import Chart1 from './components/Chart1'
import Chart2 from './components/Chart2'
import Chart3 from './components/Chart3'
import Chart4 from './components/Chart4'
import Chart5 from './components/Chart5'
import Chart6 from './components/Chart6'
import AdminList from './components/AdminList'
import AddrTurnNameList from './components/AddrTurnNameList'
import CarsTypeList from './components/CarsTypeList'
import AreaSubsidy from './components/AreaSubsidy'
import CompanyList from './components/CompanyList'
import ACompanyList from './components/ACompanyList'
import ShowList from './components/ShowList'
import CitySel from './components/CitySel'
import ExportTable from './components/ExportTable'
import QA from './components/QA'
import Element from 'element-ui'
import locale from 'element-ui/lib/locale/lang/zh-TW'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css';
import './css/index.css'
import m from './all.js'

Vue.config.productionTip = false
Vue.prototype.GLOBAL = Vue.observable(Global)
Vue.prototype.CitySel = Vue.observable(CitySel)
Vue.prototype.Function = m
Vue.prototype.all_ready = async function () {
	while (!this.GLOBAL.ready) {
		await m.delay(1)
	}
}
Vue.prototype.clear_seach = function () {
	this.GLOBAL.seach = {
		start_data: "",
		end_data: "",
		key: ""
	}
}
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return routerPush.call(this, location).catch(error => error)
}
Vue.use(VueRouter)
Vue.use(Element, { locale })

const router = new VueRouter({
	routes: [
		{ path: '/Left', component: ShowList },
		{ path: '/Left/:id', component: List },
		{ name: 'CaseList', path: '/CaseList/:page', component: CaseList },
		{ name: 'DriverList', path: '/DriverList/:page', component: DriverList },
		{ name: 'CarList', path: '/CarList/:page', component: CarList },
		{ name: 'CaseMore', path: '/CaseMore/', component: CaseMore },
		{ name: 'CaseMore', path: '/CaseMore/:id', component: CaseMore },
		{ name: 'CarMore', path: '/CarMore/', component: CarMore },
		{ name: 'CarMore', path: '/CarMore/:id', component: CarMore },
		{ name: 'DriverMore', path: '/DriverMore/', component: DriverMore },
		{ name: 'DriverMore', path: '/DriverMore/:id', component: DriverMore },
		{ name: 'DragDispatch', path: '/DragDispatch', component: DragDispatch },
		{ name: 'TableDispatch', path: '/TableDispatch', component: TableDispatch },
		{ name: 'OrderList', path: '/OrderList/:page', component: OrderList },
		{ name: 'OrderDetails', path: '/OrderDetails/:id', component: OrderDetails },
		{ name: 'Chart1', path: '/Chart1', component: Chart1 },
		{ name: 'Chart2', path: '/Chart2', component: Chart2 },
		{ name: 'Chart3', path: '/Chart3', component: Chart3 },
		{ name: 'Chart4', path: '/Chart4', component: Chart4 },
		{ name: 'Chart5', path: '/Chart5', component: Chart5 },
		{ name: 'Chart6', path: '/Chart6', component: Chart6 },
		{ name: 'AdminList', path: '/AdminList/:page', component: AdminList },
		{ name: 'AddrTurnNameList', path: '/AddrTurnNameList/:page', component: AddrTurnNameList },
		{ name: 'CarsTypeList', path: '/CarsTypeList', component: CarsTypeList },
		{ name: 'AreaSubsidy', path: '/AreaSubsidy', component: AreaSubsidy },
		{ name: 'CompanyList', path: '/CompanyList', component: CompanyList },
		{ name: 'ACompanyList', path: '/ACompanyList/:page', component: ACompanyList },
		{ name: 'QA', path: '/QA', component: QA },
	]
})
/* eslint-disable no-new */
new Vue({
	el: '#main',
	router,
	data() {
		return {
			d1: "123",
			data: "",
			json_data:[{
				d1:123,
				d2:"安安"
			}]
		}
	},
	watch: {
		$route(to, from) {
		}
	},
	created: async function () {
		this.Function.catch_retry()
		await this.re_get()
		this.GLOBAL.menu=await m.get_GetMenuIds(JSON.parse(localStorage.dk3_user).Id)
		this.GLOBAL.ready = true
		var that=this
		setInterval(async function(){ 
		  await that.re_get()
		 }, 60000);
	},
	methods:{
      re_get:async function() {
		var now_token=JSON.parse(localStorage.dk3_token).token
		localStorage.dk3_token=JSON.stringify(await m.RefreshToken(now_token))
        await m.put_token(JSON.parse(localStorage.dk3_token).token)
	  }
	},
	components: { App, Top, Left, Black,ExportTable},
	template: '<div id="main">\
	<ExportTable />\
    <Black v-if="GLOBAL.show_black"/>\
    <Top/>\
	<div class="d1">\
	  <Left/>\
	  <div class="d1-r">\
		<router-view/>\
	  </div>\
	</div>\
	</div>\
  </div>'
})



