<template>
  <div class="chart">
    <h2>本日接送狀況</h2>
    <div class="seach">
        <el-date-picker v-model="day" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
	      <el-select v-model="c_county" placeholder="請選擇地區" @change="get_data">
          <el-option :label="'請選擇地區'" :value="0"></el-option>
          <el-option v-for="(item,index) in county" :label="item.District" :value="item.Id"></el-option>
	      </el-select>
	      <el-select v-model="c_comp" placeholder="請選擇公司" @change="get_data">
          <el-option :label="'請選擇公司'" :value="0"></el-option>
          <el-option v-for="(item,index) in comp" :label="item.CompanyName" :value="item.Id"></el-option>
	      </el-select>
    </div>
    <div v-if="list&&list[0]">
  	  <div class="chart-d">
	      <div class="chart-d-h">總趟次<div style="float:right;">{{list[list.length-1].ReportPickViewModel.TotalCount}}</div></div>
	    </div>
  	  <div class="chart-d">
	      <div class="chart-d-h">已完成<div style="float:right;">{{list[list.length-1].ReportPickViewModel.CompleteCount}}</div></div>
	    </div>
  	  <div class="chart-d">
	      <div class="chart-d-h">空趟<div style="float:right;">{{list[list.length-1].ReportPickViewModel.MissCount}}</div></div>
	    </div>
  	  <div class="chart-d">
	      <div class="chart-d-h">未執行<div style="float:right;">{{list[list.length-1].ReportPickViewModel.NotPerformedCount}}</div></div>
	    </div>
  	  <div class="chart-d">
	      <div class="chart-d-h">達成率<div style="float:right;">{{list[list.length-1].ReportPickViewModel.AchievedRate}}%</div></div>
	    </div>
    </div>
    <div style="clear:both;">
      <h2>本周接送狀況</h2>
	    <canvas width="400" height="400" ref="c1"></canvas>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	    list:[],
      ctxt:"",
      county:[],
      c_county:0,
      comp:[],
      c_comp:0,
      day:""
	  }
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
    await this.all_ready()
    this.county=await this.Function.get_GetTaiwanCounty_Id(189)
    this.comp=await this.Function.get_CarCompanys()
    await this.get_data()
  	this.GLOBAL.show_black=false
  	console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function(x) {
      this.GLOBAL.show_black=true
	    this.list=await this.Function.get_GetDatePickWeekly(this.day,this.c_county,this.c_comp).catch(e=>{alert("發生錯誤，請再試一次");this.GLOBAL.show_black=false})  
        this.list.reverse()
        var arr1=this.list.map(x=>x.ReportPickViewModel.AchievedRate)
			  var arr2=this.list.map(x=>x.ReportPickViewModel.CompleteCount)
			  var arr3=this.list.map(x=>x.ReportPickViewModel.MissCount)
			  var arr4=this.list.map(x=>x.ReportPickViewModel.NotPerformedCount)
			  var arr5=this.list.map(x=>x.ReportPickViewModel.TotalCount)
			  var arr6=this.list.map(x=>this.Function.moment.yyyymmdd(x.DetailDate))
		      var obj=[
                {
	              label: '已完成',
				  fill: false,
                  backgroundColor: 'rgb(119, 255, 0)',
				  borderColor: 'rgb(119, 255, 0)',
                  data: arr2
                },
                {
	              label: '空趟',
				  fill: false,
                  backgroundColor: 'rgb(220, 238, 17)',
				  borderColor: 'rgb(220, 238, 17)',
                  data: arr3
                },
                {
	              label: '未執行',
				  fill: false,
                  backgroundColor: 'rgb(238, 28, 17)',
				  borderColor: 'rgb(238, 28, 17)',
                  data: arr4
                },
                {
	              label: '總趟次',
				  fill: false,
                  backgroundColor: 'rgb(17, 153, 238)',
				  borderColor: 'rgb(17, 153, 238)',
                  data: arr5
                },
              ]
              var ctx = this.$refs.c1
              if(this.ctxt) {
                this.ctxt.destroy()
              }
              this.ctxt=this.Function.chart.drew_chart(ctx,'line',arr6,obj,true)
              this.GLOBAL.show_black=false
	}
  }
}
</script>



