<template>
  <div class="mbox" v-if="open">
    <div class="mbox-black" @click="switch_open"></div>
    <div class="mbox-d">
      <div class="mbox-d-h">
        {{title}}
        <i class="el-icon-close mbox-d-h-x" @click="switch_open"></i>
      </div>
      <div class="mbox-d-c">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  props:["title"],
  data() {
    return {
      open:false
	}
  },
  mounted :async function() {
    Vue.prototype.MBox=this
    this.re_size()
  },
  methods:{
    re_size:async function() {
      var dom=document.querySelectorAll(".mbox-d")[0]
      if (!dom) {
        return 0
      }
      var w=dom.offsetWidth
      var h=dom.offsetHeight
      dom.style.margin=-(h/2)+"px 0 0 "+-(w/2)+"px"
      console.log(dom)
    },
    switch_open:async function() {
      this.open=!this.open
      await this.Function.delay(0)
      this.re_size()
    }
  }
}
</script>



