<template>
  <div class="more">
    <h2>補助車資參數</h2>
	<div class="more-b">
	  <div class="more-c">
		<el-button type="primary" icon="el-icon-document-checked" @click="sub()">存檔</el-button>
        <div class="more-d">
		  <span>起跳金額:</span>
		  <el-input placeholder="" v-model="list.BasicAmt"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>起跳公尺數:</span>
		  <el-input placeholder="" v-model="list.BasicM"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>續跳公尺數:</span>
		  <el-input placeholder="" v-model="list.JumpM"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>續跳金額:</span>
		  <el-input placeholder="" v-model="list.JumpAmt"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>國道通行費:</span>
		  <el-input placeholder="" v-model="list.HighwayMultiple"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>夜間加成:</span>
		  <el-input placeholder="" v-model="list.NightMultiple"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>服務費:</span>
		  <el-input placeholder="" v-model="list.ServiceAmt"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>平地一般戶:</span>
		  <el-input placeholder="" v-model="list.SubsidyMode2ViewModel[1].SelfMultiple"></el-input>
		  <hr>
		</div>
      </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>平地中低收:</span>
		  <el-input placeholder="" v-model="list.SubsidyMode2ViewModel[3].SelfMultiple"></el-input>
		  <hr>
		</div>
      </div>
	  	  <div class="more-c">
        <div class="more-d">
		  <span>平地低收:</span>
		  <el-input placeholder="" v-model="list.SubsidyMode2ViewModel[5].SelfMultiple"></el-input>
		  <hr>
		</div>
      </div>
	  	  <div class="more-c">
        <div class="more-d">
		  <span>偏區一般戶:</span>
		  <el-input placeholder="" v-model="list.SubsidyMode2ViewModel[0].SelfMultiple"></el-input>
		  <hr>
		</div>
      </div>
	  	  <div class="more-c">
        <div class="more-d">
		  <span>偏區中低收:</span>
		  <el-input placeholder="" v-model="list.SubsidyMode2ViewModel[2].SelfMultiple"></el-input>
		  <hr>
		</div>
      </div>
	  	  <div class="more-c">
        <div class="more-d">
		  <span>偏區低收:</span>
		  <el-input placeholder="" v-model="list.SubsidyMode2ViewModel[4].SelfMultiple"></el-input>
		  <hr>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[]
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(189)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_SubsidyAmtCountry(x)
	  console.log(this.list)
	},
	sub:async function() {
	  this.GLOBAL.show_black=true
	  alert(await this.Function.put_SubsidyAmtCountry(this.list))
	  await this.get_data(189)
      this.GLOBAL.show_black=false
	},
  }
}
</script>



