<template>
  <div class="qa">
    <h2>常見問題Q&A</h2>
    <div>
      <div>
        <h3>Q1.查無個案資料無法下訂單，怎麼辦?</h3>
        <div>A1.交通平台系統目前無法與衛福部CMS系統介接，以人工半自動方式將個案清冊下載後匯入交通平台系統，故有1~1.5天的時間差。若查無個案資料，可聯絡系統商侯小姐詢問(TEL：2257-7155#3681)。</div>
      </div>
      <div>
        <h3>Q2.訂單無顯示價錢，或預約後顯示去程、或回程地址錯誤，怎麼辦?</h3>
        <div>A1.交通平台系統目前無法與衛福部CMS系統介接，以人工半自動方式將個案清冊下載後匯入交通平台系統，故有1~1.5天的時間差。若查無個案資料，可聯絡系統商侯小姐詢問(TEL：2257-7155#3681)。</div>
      </div>
      <div>
        <h3>Q3.輸入醫院名稱，為什麼出現很多選項?</h3>
        <div>A3.以台大醫院為例，有常德街舊址、中山南路新址、兒童醫院，因此需與個案或家屬確認正確上下車位置。</div>
      </div>
      <div>
        <h3>Q4.來回趟為什麼價錢不一樣?</h3>
        <div>A4.交通平台系統是以google map最短距離報價，去程回程的距離是不一樣的，有單行道、有迴轉、有上下橋等路線差異，故去程回程的距離及價錢可能有所不同。</div>
      </div>
      <div>
        <h3>Q5.共乘定義?共乘價格如何計算?</h3>
        <div>A5.實際有二組長照個案在車上，若有一組個案臨時取消預約服務，另一組個案無共乘折扣，依據原本報價收費。臨時取消服務者記違規1點做懲處。
雙方共乘車資總價都*66%，再與個案個別收取自負額，陪同者另行計價。</div>
      </div>
      <div>
        <h3>Q6.司機打卡後，還能推播新訂單嗎?哪裡可以看明天的任務?</h3>
        <div>A6.可以。「今日任務」：當天所有接送任務、「任務歷程」：可依日期查看所有接送任務。</div>
      </div>
      <div>
        <h3>Q7.系統裡建置的司機和車輛資料，只可新增但無法刪除？ </h3>
        <div>A7.請建置衛生局核備合格的司機和車輛資料，以利衛生局行政人員稽核。</div>
      </div>
      <div>
        <h3>Q8.因勞基法規定工時，故可能有多位司機開一部車情況，如何將訂單改換司機?</h3>
        <div>A8.單筆訂單欲更換司機：於調度控制台中找到該筆訂單，點擊右鍵即可更換駕駛（不換車）。<br>當天所有訂單欲更換司機：可於司機車輛設定-車輛資料中，直接更換該車駕駛</div>
      </div>
      <div>
        <h3>Q9.司機如何下載及安裝司機端APP?</h3>
        <div>A9.
          <ol>
            <li>Android手機請直接點選營運端上方google play圖示，即可下載apk檔進行安裝。</li>
            <li>ios手機安裝步驟：
              <ol>
                <li>將手機連接至電腦後開啟iTunes應用程式</li>
                <li><div>點選手機icon</div><img src="./img/QA1.png"></li>
                <li><div>在「序號」的欄位點一下</div><img src="./img/QA2.png"></li>
                <li><div>在「UDID」的欄位點擊右鍵拷貝，即可複製UDID</div><img src="./img/QA3.png"></li>
                <li>填寫表單申請後，相關負責人員將提供ipa檔</li>
                <li><div>電腦下載itools3，即可安裝ipa檔</div><img src="./img/QA4.png"><img src="./img/QA5.png"></li>
              </ol>
            </li>
          </ol>
        </div>
      </div>
      <div>
        <h3>Q10.司機變更手機號碼，司機端APP的帳號卻沒有跟著改變?</h3>
        <div>A10.首次登錄之手機號碼及為司機端APP帳號，帳號產生後便不會改變，若有特殊原因須更改帳號，請向衛生局申請變更帳號需求單，並註明變更原因。</div>
      </div>
      <div>
        <h3>Q11.如何進行每月車資核銷?</h3>
        <div>A11.從個案資料裡的全部個案資料，右上角有匯出Excel鍵，可批次上傳至衛福部CMS系統核銷(QD-107新制服務區->110-服務紀錄匯入->上傳交通平台系統所匯出的報表)。</div>
      </div>
      <div>
        <h3>Q12.司機直接送任務時沒網路訊號，不能操作APP怎麼辦?</h3>
        <div>A12.每日出勤前可先列印車輛使用證明單或任務明細檔供司機備用，等網路有訊號後再將接送任務按完，營運端可於預約訂單之行車歷程中補登時間</div>
        <img src="./img/QA6.png">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  }
  },
  mounted:async function() {

  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{

  }
}
</script>



