<template>
  <div class="more">
    <h2>車輛詳細資料</h2>
    <MBox :title="edit ? '編輯保養紀錄':'新增保養紀錄'" ref="mb1">
      <div class="admin-list">
        <el-row>
          <el-col :span="8">檢查日期</el-col>
          <el-col :span="16">
			<el-date-picker v-model="c_mlist.MaintainDate" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd"></el-date-picker>
		  </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">里程</el-col>
          <el-col :span="16"><el-input v-model="c_mlist.MaintainMiles"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">保養項目</el-col>
          <el-col :span="16"><el-input v-model="c_mlist.MaintainContent"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">金額</el-col>
          <el-col :span="16"><el-input v-model="c_mlist.Amt"></el-input></el-col>
        </el-row>
        <div class="bt" @click="sub_mlist()">確定提交</div>
      </div>
    </MBox>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false" v-if="this.$route.params.id">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">{{this.$route.params.id ? "編輯":"新增"}}</div>
	  </div>
	  <div class="more-c">
	    <img :src="list.CarPic" class="avatar" v-if="!edit">
        <el-upload class="avatar-uploader" :action="GLOBAL.up_img" :show-file-list="false" :on-success="upload_img_ok" v-if="edit">
          <img v-if="list.CarPic" :src="list.CarPic" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
		<el-button type="primary" icon="el-icon-document-checked" @click="sub()" v-if="edit">存檔</el-button>
        <div class="more-d">
		  <span>狀態: <span v-if="!edit">{{list.Status ? "可派發" : "不可派發"}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.Status" placeholder="請選擇">
              <el-option :label="'可派發'" :value="1"></el-option>
			  <el-option :label="'不可派發'" :value="0"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>司機: <span class="red" v-if="edit">(必填)</span><span v-if="!edit&&list.driver">{{list.driver.DriverName}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.DriverId" placeholder="請選擇">
              <el-option v-for="(item,index) in driver_list" :label="item.DriverName" :value="item.Id"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>出廠年月: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{Function.moment.yyyymmdd(list.MakeDate)}}</span></span>
		  <div class="more-d-edit" v-if="edit">
            <el-date-picker v-model="list.MakeDate" value-format="yyyy-MM" type="month"></el-date-picker>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>最後驗車: <span v-if="!edit">{{Function.moment.yyyymmdd(list.LastCheckDate)}}</span></span>
		  <div class="more-d-edit" v-if="edit">
			<el-date-picker v-model="list.LastCheckDate" value-format="yyyy-MM-dd" type="date"></el-date-picker>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車身顏色: <span v-if="!edit">{{list.CarColor}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input v-model="list.CarColor"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span v-if="list.CarCategory">類別: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.CarCategory[0].CarTypeName}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.CarCategory[0].Id">
              <el-option :label="'一般車'" :value="236"></el-option>
			  <el-option :label="'福祉車'" :value="214"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車牌號碼: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.CarNo}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input v-model="list.CarNo"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>廠牌型號: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.FactoryType}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input v-model="list.FactoryType"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車輛來源: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.CarFrom==2 ? "捐贈":list.CarFrom==3 ? "自購":"獎助"}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.CarFrom">
              <el-option v-for="(item,index) in list4" :label="item.CarTypeName" :value="item.Id"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車頂高度: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.CarTop==225 ? "正常":"高頂"}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.CarTop">
              <el-option :label="'正常'" :value="225"></el-option>
			  <el-option :label="'高頂'" :value="224"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>座位數量: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.SeatNum}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input-number v-model="list.SeatNum" :min="0"></el-input-number>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>輪椅數量: <span v-if="!edit">{{list.WheelchairNum}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input-number v-model="list.WheelchairNum" :min="0"></el-input-number>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車輛保險:<el-tag v-for="(item,index) in list.CarSecure">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="Id" v-model="list.CarSecure" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in list2" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車輛設備:<el-tag v-for="(item,index) in list.CarDevice">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="Id" v-model="list.CarDevice" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in list1" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>適用駕照類型:<el-tag v-for="(item,index) in list.CarLicense">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="Id" v-model="list.CarLicense" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in list3" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
		<div class="more-c-d">
          <div class="more-c-d-h">保養紀錄
			<div style="float:right;">  
			  <el-button @click="edit=false;c_mlist=empty;$refs.mb1.switch_open()">新增</el-button>
			  <el-button @click="get_mlist(true)">加載</el-button>
		    </div>
		  </div>
		  <div class="more-c-d-c">
            <el-table :data="mlist.data" style="width: 100%" v-if="mlist.data">
              <el-table-column label="車牌號碼" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.Cars.CarNo}}
                </template>
			  </el-table-column>
              <el-table-column label="里程" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.MaintainMiles}}
                </template>
			  </el-table-column>
              <el-table-column label="保養項目" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.MaintainContent}}
                </template>
			  </el-table-column>
              <el-table-column label="金額" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.Amt}}
                </template>
			  </el-table-column>
              <el-table-column label="建立時間" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  {{scope.row.CreateTime ? Function.moment.yyyymmdd(scope.row.CreateTime) : "無"}}
                </template>
			  </el-table-column>
              <el-table-column label="操作" style="width:calc(100% / 6);">
			    <template slot-scope="scope">
                  <el-button @click="edit=true;c_mlist=scope.row;$refs.mb1.switch_open()">修改</el-button>
                </template>
			  </el-table-column>
            </el-table>	
		  </div>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
import MBox from './MBox'
export default {
  data() {
    return {
	  list_key:[],
	  edit:false,
	  all_type:"",
	  list1:"",
	  list2:"",
	  list3:"",
	  list4:"",
	  mlist:[],
	  mlist_page:1,
	  edit:false,
	  driver_list:"",
	  empty:{
		MaintainDate:"",
		MaintainMiles:"",
		MaintainContent:"",
		Amt:""
	  },
      list: {
        Id: 0,
        Status: 0, //可否派發
        CarPic: "", //車輛圖片
        MakeDate: "", //製造日期
        LastCheckDate: "", //最後驗車日
        CompanyId: 0, //車行
        CarTypes: [], //類別
        CarNo: "", //車牌號碼
        FactoryType: "", //廠牌型號
        CarFrom: 0, //車輛來源
        DonationUnit: "", //捐贈單位
        CarPhone: "", //綁定手機
        VariableSpeed: "", //變速系統
        EngineType: "", //引擎行事
        MaxPower: "", //最大馬力
        ModeMethod: "", //傳動方式
        CarCC: "", //排氣量
        WheelchairNum: 0, //輪椅數量
        CarFuel: 0, //用油燃料
        SeatNum: 0, //座椅數量
        Remark: "", //備註
        CarTop: "",
		//類別  核定服務  車輛服務   適用駕照類型
        CarTypes: [
          {
            Id: 0
          }
		],
		CarCategory:[
		  {Id:236}	
		]
        },
	  c_mlist:""
	}
  },
  components: {MBox},
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
	if(!this.$route.params.id) this.edit=true 
	this.driver_list=await this.Function.get_driverNoPage().catch(e=>{location.reload();})
	this.all_type=await this.Function.get_CarsType_all().catch(e=>{location.reload();})
	this.list1=this.all_type.filter(re=>re.ParentID==7)
	this.list2=this.all_type.filter(re=>re.ParentID==6)
	this.list3=this.all_type.filter(re=>re.ParentID==9)
	this.list4=this.all_type.filter(re=>re.ParentID==1)
	if(this.$route.params.id) await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
	  await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_car_more(x).catch(e=>{location.reload();})
	  this.list.driver=this.driver_list.filter(re=>re.Id==this.list.DriverId)[0]
	  console.log(this.list)
	},
	get_mlist:async function(x) {
	  this.GLOBAL.show_black=true
	  this.mlist=await this.Function.get_GetCarMaintainByCid(this.$route.params.id,1)
	  if(x) this.mlist_page++
	  this.GLOBAL.show_black=false
	},
	sub_mlist:async function() {
	  if(!this.check_input2()) return false
	  this.c_mlist.CarId=this.$route.params.id
	  this.GLOBAL.show_black=true
      if(this.edit) {
		alert(await this.Function.put_PutCarMaintain(this.c_mlist))
	  }
	  else {
        alert(await this.Function.post_PostCarMaintain(this.c_mlist))
	  }
	  await this.get_mlist()
	  this.GLOBAL.show_black=false
	},
	upload_img_ok:function(res) {
	  this.$message(res.msg)
	  this.list.CarPic=this.GLOBAL.call_img+res.response
	},
	check_input:function() {
	  if(!this.list.DriverId) {
		alert("司機未選")
		return false
	  }
	  if(!this.list.MakeDate) {
		alert("出廠年月未填")
		return false
	  }
	  if(!this.list.CarCategory[0].Id) {
		alert("類別未填")
		return false
	  }
	  if(!this.list.CarNo) {
		alert("車牌號碼未填")
		return false
	  }
	  if(!this.list.FactoryType) {
		alert("廠牌型號未填")
		return false
	  }
	  if(!this.list.CarFrom) {
		alert("車輛來源未填")
		return false
	  }
	  if(!this.list.CarTop) {
		alert("車頂高度未填")
		return false
	  }
	  if(!this.list.SeatNum) {
		alert("座位數量未填")
		return false
	  }
      return true
	},
	check_input2:function() {
	  if(!this.c_mlist.MaintainDate) {
		alert("檢查日期未填")
		return false
	  }
	  if(!this.c_mlist.MaintainMiles) {
		alert("里程未填")
		return false
	  }
	  if(!this.c_mlist.MaintainContent) {
		alert("保養項目未填")
		return false
	  }
	  if(!this.c_mlist.Amt) {
		alert("金額未填")
		return false
	  }
      return true
	},
	sub:async function() {
	  if(!this.check_input()) return false
	  this.GLOBAL.show_black=true
      if(this.$route.params.id) {
        alert(await this.Function.put_car(this.list))
	  }
	  else {
        alert(await this.Function.post_car(this.list))
	  }
	  history.go(-1)
	  this.GLOBAL.show_black=false
	}
  }
}
</script>



