<template>
  <div v-if="list.OrderDetails&&show===1">
    <MBox :title="'修改時間'" ref="mb1">
      <div style="width:300px;">
        <el-date-picker v-model="c_time.day" type="date" style="width:100%;" placeholder="選擇日期" value-format="yyyy-MM-dd"></el-date-picker>
        <el-input v-model="c_time.time" placeholder="輸入時間(格式:hh:mm)"></el-input>
        <el-input v-model="c_time.amt" placeholder="實收金額" v-if="c_s==6"></el-input>
        <el-input v-model="c_time.remark" placeholder="備註" v-if="c_s==6"></el-input>
        <el-select v-model="c_time.num" placeholder="實際陪同人數" v-if="c_s==6">
          <el-option v-for="(item,index) in 7" :label="item" :value="item"></el-option>
        </el-select>
        <el-select v-model="c_time.share" placeholder="是否共乘" v-if="c_s==6">
          <el-option :label="'是'" :value="'true'"></el-option>
          <el-option :label="'否'" :value="'false'"></el-option>
        </el-select>
        <div class="bt" @click="sub()">確定提交</div>
      </div>
    </MBox>
	  <div class="map" ref="map1"></div>
      <div>
        <div class="order-s-d"><span>預估時間:</span><span class="order-d-d-s">{{list.OrderDetails.ExpectedMinute}} 分鐘</span></div>
        <div class="order-s-d"><span>預估里程:</span><span class="order-d-d-s">{{list.OrderDetails.TotalMileage / 1000}} 公里</span></div>
        <div class="order-s-d"><span>預約搭車時間:</span><span class="order-d-d-s">{{Function.moment.yyyymmddhhmm(list.OrderDetails.ReservationDate)}}</span></div>
        <div class="order-s-d"><span>建立時間:</span><span class="order-d-d-s">{{Function.moment.yyyymmddhhmm(list.OrderDetails.CreateTime)}}</span></div>
      </div>
      <div class="order-d-d">
        <h3>{{list.OrderDetails.SOrderNo}}</h3>
        <div>{{list.OrderDetails.FromAddr}}</div>
        <i class="el-icon-bottom"></i>
        <div style="margin:0 0 10px 0;">{{list.OrderDetails.ToAddr}}</div>
        <div class="order-line">
          <div class="order-line-d" v-for="(item,index) in list.OrderStatusLog">
            <div class="order-line-d-r"></div>
            <div class="order-line-d-line" v-if="index!=list.OrderStatusLog.length-1"></div>
            <div class="order-line-d-data">
              <div class="order-line-d-d">{{item.StatusChinese}}<span class="red" style="cursor:pointer;" @click="c_s=item.Status;$refs.mb1.switch_open()">(修改)</span></div>
              <div class="order-line-d-time">{{Function.moment.yyyymmddhhmm(item.CreateDate)}}</div>
            </div>
          </div>
        </div>
        <div style="clear:both;">
        <div class="order-d-d-d">
          <div>車資總額</div>
          <div>${{list.OrderDetails.TotalAmt-list.OrderDetails.OtherAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>政府補助</div>
          <div>${{list.OrderDetails.TotalAmt-list.OrderDetails.SelfPayAmt-list.OrderDetails.OtherAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>陪同金額</div>
          <div>${{list.OrderDetails.OtherAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>實際應收</div>
          <div>${{list.OrderDetails.RealTotalAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>實際實收</div>
          <div>${{list.OrderDetails.ReceivedAmt}}</div>
        </div>
        <div class="order-d-d-d">
          <div>陪同人數</div>
          <div>{{list.OrderDetails.FamilyWith}}</div>
        </div>
        <div class="order-d-d-d">
          <div>經濟狀況</div>
          <div>{{list.CaseUser.UserTypeChinese}}</div>
        </div>
        </div>
      </div>
  </div>
</template>

<script>
import MBox from './MBox'
export default {
  props:["list"],
  data() {
    return {
    maps:[],
    show:1,
    c_s:0,
    c_time:{
      day:"",
      time:"",
      amt:"",
      remark:"",
      num:"",
      share:""
    }
	}
  },
  components: {MBox},
  mounted:async function() {
    this.show_map()
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    show_map:async function() {
        var dom=document.querySelectorAll(".map")
        this.maps[0]=this.Function.initMap(dom[0])
	      this.Function.calculateAndDisplayRoute(this.maps[0],this.list.OrderDetails.FromAddr,this.list.OrderDetails.ToAddr)
    },
    sub:async function() {
      this.GLOBAL.show_black=true
      if(this.c_s==6) {
        this.$message(await this.Function.put_PutDetailStatusAfter2(this.$route.params.id,this.c_time.amt,this.c_time.remark,this.c_time.num,this.c_time.share,this.c_s,this.c_time.day,this.c_time.time))
      }
      else {
        this.$message(await this.Function.put_PutDetailStatusAfter(this.$route.params.id,this.c_s,this.c_time.day,this.c_time.time))
      }
      location.reload()
      this.GLOBAL.show_black=false
    }
  }
}
</script>



