<template>
  <div class="more">
    <h2>司機詳細資料</h2>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false" v-if="$route.params.id">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">{{$route.params.id ? "編輯":"新增"}}</div>
	  </div>
	  <div class="more-c">
		<div class="more-c-data">
		  <img :src="list.Pic" class="avatar" v-if="!edit">
          <el-upload class="avatar-uploader" :action="GLOBAL.up_img" :show-file-list="false" :on-success="upload_img_ok" v-if="edit">
            <img v-if="list.Pic" :src="list.Pic" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
		  <el-button type="primary" icon="el-icon-document-checked" @click="sub()" v-if="edit">存檔</el-button>
		</div>
        <div class="more-d">
		  <span>可否派發: <span v-if="!edit">{{list.Status ? "可派發":"不可派發"}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.Status">
			  <el-option :label="'可派發'" :value="1"></el-option>
			  <el-option :label="'不可派發'" :value="0"></el-option>
			</el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>姓名: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.DriverName}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入姓名" v-model="list.DriverName"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>手機: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.Phone}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入手機:" v-model="list.Phone"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>性別: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.Sex==1 ? "男" : "女"}}</span></span>
		  <div class="more-d-edit" v-if="edit">
            <el-select v-model="list.Sex" placeholder="請選擇">
			  <el-option :label="'男'" value=1>男</el-option>
			  <el-option :label="'女'" value=2>女</el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>市話: <span v-if="!edit">{{list.Tel}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入市話" v-model="list.Tel"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>身分證字號: <span class="red" v-if="edit">(必填)</span><span v-if="!edit">{{list.UID}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入身分證字號" v-model="list.UID"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>Email: <span v-if="!edit">{{list.Email}}</span></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入Email" v-model="list.Email"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>駕照:<el-tag v-for="(item,index) in list_DriverLicense">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="TypeId" v-model="list_DriverLicense" multiple placeholder="請選擇" @change="$forceUpdate()">
              <el-option v-for="(item,index) in DriverLicense" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <div v-for="(item,index) in list_DriverLicense" v-if="edit">
			<div>{{item.CarTypeName}}</div>
		    <el-input style="width:50%;float:left;" v-model="item.LicenseNo" :placeholder="'請輸入駕照編號'"></el-input>
		    <el-date-picker style="width:50%;" v-model="item.ExpiredDate" value-format="yyyy-MM-dd" type="date" placeholder="選擇到期日" @change="show_c(item.ExpiredDate)"></el-date-picker>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>保險:<el-tag v-for="(item,index) in list_DriverSecure">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="TypeId" v-model="list_DriverSecure" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in DriverSecure" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>持有證照:<el-tag v-for="(item,index) in list_DriverProLicense">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="TypeId" v-model="list_DriverProLicense" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in DriverProLicense" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
		<div class="more-c-d" @click="get_punch()" v-if="!edit">
          <div class="more-c-d-h">打卡紀錄</div>
		  <div class="more-c-d-c">
            <el-table :data="punch" style="width: 100%" v-if="punch!==false">
              <el-table-column label="日期" style="width:calc(100% / 3);">
			    <template slot-scope="scope">
                  {{Function.moment.yyyymmdd(scope.row.CreateDate)}}
                </template>
			  </el-table-column>
              <el-table-column label="上班時間" style="width:calc(100% / 3);">
			    <template slot-scope="scope">
                  {{scope.row.OnTime ? Function.moment.yyyymmdd(scope.row.OnTime) : "無"}}
                </template>
			  </el-table-column>
              <el-table-column label="下班時間" style="width:calc(100% / 3);">
			    <template slot-scope="scope">
                  {{scope.row.OffTime ? Function.moment.yyyymmdd(scope.row.OffTime) : "無"}}
                </template>
			  </el-table-column>
            </el-table>		    	
		  </div>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:{
  "Id": 0,
  "CompanyId": 0,
  "Account": "",
  "Pwd": "",
  "Sex": "",
  "Pic": "",
  "DriverName": "",
  "Phone": "",
  "UID": "",
  "Tel": "",
  "Email": "",
  "County": "",
  "District": "",
  "Addr": "",
  "Remark": "",
  "Status": 1,
  "IsDeleted": true,
  "Enabled": true,
  "CreateId": 0,
  "CreateBy": "string",
  "CreateTime": "2019-10-24T09:51:59.267Z",
  "ModifyId": 0,
  "ModifyBy": "string",
  "ModifyTime": "2019-10-24T09:51:59.267Z",
  "DriverSaveTypes": [
    {
      "Id": 0,
      "DriverId": 0,
      "TypeId": 160,
      "ExpiredDate": "2019-10-24T09:51:59.267Z"
    }
  ]
},
	  list_key:[],
	  DriverLicense:"",
	  list_DriverLicense:"",
	  DriverSecure:"",
	  list_DriverSecure:"",
	  DriverProLicense:"",
	  list_DriverProLicense:"",
	  edit:false,
	  punch:false,
	  health:"",
	  all_type:""
	}
  },
  mounted:async function() {
	this.GLOBAL.show_black=true
	if(!this.$route.params.id) {
	  this.edit=true
	}
	await this.all_ready()
    await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  if(x) {
		this.list=await this.Function.get_driver_more_edit(x)
	    this.list.DriverSaveTypes.map((item,index)=>{
		  this.list.DriverSaveTypes[index]={CarTypeName:item.CarType.CarTypeName,TypeId:item.TypeId,ExpiredDate:item.ExpiredDate,LicenseNo:item.LicenseNo,ParentID:item.CarType.ParentID}
		})
		this.list_DriverLicense=this.list.DriverSaveTypes.filter(re=>re.ParentID==9)
	    this.list_DriverSecure=this.list.DriverSaveTypes.filter(re=>re.ParentID==206)
	    this.list_DriverProLicense=this.list.DriverSaveTypes.filter(re=>re.ParentID==162)
		console.log(1111)
		console.log(this.list_DriverLicense)
	  }
	  this.all_type=await this.Function.get_CarsType_all()
      this.all_type.map((item,index)=>{
		this.all_type[index]={CarTypeName:item.CarTypeName,TypeId:item.Id,ExpiredDate:"",LicenseNo:"",ParentID:item.ParentID}
	  })
	  this.DriverLicense=this.all_type.filter(re=>re.ParentID==9)
	  this.DriverSecure=this.all_type.filter(re=>re.ParentID==206)
	  this.DriverProLicense=this.all_type.filter(re=>re.ParentID==162)
	},
	show_c:function(x) {
	  console.log(x)
	},
	get_punch:async function() {
	  this.GLOBAL.show_black=true
	  this.punch=await this.Function.get_PunchByDriver(this.$route.params.id)
	  this.GLOBAL.show_black=false
	},
	upload_img_ok:function(res) {
	  this.$message(res.msg)
	  this.list.Pic=this.GLOBAL.call_img+res.response
	},
	check_input:function() {
	  if(!this.list.DriverName) {
		alert("姓名未填")
		return false
	  }
	  if(!this.list.Phone) {
		alert("手機未填")
		return false
	  }
	  if(!this.list.UID||!this.Function.check.check_uid(this.list.UID)) {
		alert("身分證字號未填")
		return false
	  }
	  if(!this.list.Sex) {
		alert("性別未填")
		return false
	  }
      return true
	},
	sub:async function() {
	  if(!this.check_input()) return false
	  this.GLOBAL.show_black=true
	  this.list.DriverSaveTypes=this.list_DriverLicense.concat(this.list_DriverSecure).concat(this.list_DriverProLicense)
      if(this.$route.params.id) {
        alert(await this.Function.put_driver(this.list))
	  }
	  else {
        alert(await this.Function.post_driver(this.list))
	  }
	  //await this.get_data(this.$route.params.id)
	  history.go(-1)
      this.GLOBAL.show_black=false
	},
  }
}
</script>



