  catch_retry:function() {  //偵測error，若有error就再試一次
    axios.interceptors.response.use(null, (error) => {
      if (error.config && error.response&&error.response.status==500) {
        return axios.request(error.config);
      }
      return Promise.reject(error);
    });
  },
