  initMap: function(x) {
    return new google.maps.Map(x, {
      zoom: 7,
      center: { lat: 41.85, lng: -87.65 }
    });
  },
  calculateAndDisplayRoute: function(x,from,to) {
    var directionsService = new google.maps.DirectionsService();
    var directionsRenderer = new google.maps.DirectionsRenderer();
    directionsRenderer.setMap(x);
    directionsService.route(
      {
        origin: { query: from },
        destination: { query: to },
        provideRouteAlternatives: true,
        avoidTolls: true,
        travelMode: "DRIVING"
      },
      (response, status) => {
        if (status === "OK") {
          response.routes.sort(function (a, b) {
            return a.legs[0].distance.value - b.legs[0].distance.value;
          });
          response.routes=[response.routes[0]]
          directionsRenderer.setDirections(response);
        }
      }
    )
  },
