<template>
  <div class="list">
    <MBox :title="'新增核定地點'">
      <div class="admin-list">
        <el-row>
          <el-col :span="8">名稱</el-col>
          <el-col :span="16"><el-input v-model="c_list.AddrName"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">市話</el-col>
          <el-col :span="16"><el-input v-model="c_list.Phone"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">緯度</el-col>
          <el-col :span="16"><el-input v-model="c_list.lat"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">經度</el-col>
          <el-col :span="16"><el-input v-model="c_list.lon"></el-input></el-col>
        </el-row>
        <el-row>
          <el-col :span="8">地址</el-col>
          <el-col :span="16">
            <el-select v-model="c_list.CountyId" placeholder="請選擇縣市" :span="4">
              <el-option v-for="(item,index) in CitySel.city_sel.city_no" :key="index" :label="item" :value="item"></el-option>
            </el-select>  
		        <el-select v-model="c_list.DistinctId" placeholder="請選擇地區" :span="4">
              <el-option v-for="(item,index) in CitySel.city_sel[c_list.CountyId]" :key="index" :label="item" :value="item"></el-option>
            </el-select> 
            <el-input v-model="c_list.Addr"></el-input>
          </el-col>
        </el-row>
        <div class="bt" @click="sub()">確定提交</div>
      </div>
    </MBox>
    <h2>核定地點管理</h2>
    <div class="seach">
        <el-input placeholder="輸入關鍵字" v-model="seach.key" prefix-icon="el-icon-search"></el-input>
        <el-select v-model="seach.cno">
          <el-option label="請選擇縣市" value=""></el-option>
          <el-option v-for="(item,index) in clist" :key="index" :label="item.County" :value="item.County"></el-option>
	    </el-select>
      <el-button type="primary" icon="el-icon-search" @click="show_c">搜尋</el-button>
      <el-button type="primary" icon="el-icon-circle-plus-outline" @click="c_list=empty;is_post=true;MBox.switch_open()">新增</el-button>
    </div>
    <div class="list-d case-list list-title">
      <el-row>
        <el-col :xl="9" :md="24" :sm="24" :xs="24">名稱</el-col>
        <el-col :xl="5" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">聯絡電話</el-col>
        <el-col :xl="2" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">縣市</el-col>
        <el-col :xl="2" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">區域</el-col>
        <el-col :xl="6" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">地址</el-col>
      </el-row>
    </div>
    <div class="list-d case-list" v-for="(item,index) in list.data">
      <el-row>
        <el-col :xl="9" :md="24" :sm="24" :xs="24">{{item.AddrName}}</el-col>
        <el-col :xl="5" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">{{item.Phone}}</el-col>
        <el-col :xl="2" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">{{item.CountyId}}</el-col>
        <el-col :xl="2" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">{{item.DistinctId}}</el-col>
        <el-col :xl="6" :md="5" :sm="5" :xs="5" class="hidden-md-and-down">{{item.Addr}}</el-col>
      </el-row>
      <div class="list-d-menu">
        <i class="el-icon-tickets" title="更多" @click="c_list=item;is_post=false;MBox.switch_open()"></i>
        <i class="el-icon-delete hidden-md-and-down" title="刪除" @click="del(item)"></i>
      </div>
    </div>
	<div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'AddrTurnNameList'" :q="seach" />
  </div>
</template>

<script>
import Vue from 'vue'
import Seach from './Seach'
import Page from './Page'
import MBox from './MBox'

export default {
  data() {
    return {
      list:[],
      c_list:"",
      clist:[],
      is_post:true,
      empty:{
        "Id": 0,
        "CountyId": 0,
        "DistinctId": 0,
        "Phone": "",
        "AddrName": "",
        "Addr": "",
        "lat":"",
		    "lon":"",
      },
      seach:this.Function.p_json(this.$route.query)
	  }
  },
  components: {Page,Seach,MBox},
  mounted:async function() {
    this.GLOBAL.show_black=true
	  await this.all_ready()
    await this.get_data()
	  this.GLOBAL.show_black=false
    console.log(this.$route)
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
      this.seach=this.Function.p_json(this.$route.query)
      await this.get_data()
	    this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function() {
        this.clist=await this.Function.get_GetTaiwanCounty()
        this.list=await this.Function.get_AddrTurnName(this.$route.params.page)
	    console.log(this.list)
	  },
    show_c:async function() {
      console.log(this.$route)
      this.$router.push({ name:"CaseList" ,params:{page:1} ,query: this.seach})
    },
	  sub:async function() {
      this.GLOBAL.show_black=true
	    if(this.is_post) {
        alert(await this.Function.post_AddrTurnName(this.c_list))
		  }
			else {
			  alert(await this.Function.put_AddrTurnName(this.c_list))
      }
      await this.get_data()
      this.GLOBAL.show_black=false
      this.MBox.switch_open()
		}

	}
}
</script>



