<template>
  <div class="chart">
    <h2>出發區域比率</h2>
  <div class="seach">
      <el-date-picker v-model="s_day" type="date" placeholder="選擇日期區間(起)" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
      <el-date-picker v-model="e_day" type="date" placeholder="選擇日期區間(迄)" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
	    <el-select v-model="c_county" placeholder="請選擇地區" @change="get_data">
        <el-option :label="'請選擇地區'" :value="0"></el-option>
        <el-option v-for="(item,index) in county" :label="item.District" :value="item.Id"></el-option>
	    </el-select>
	    <el-select v-model="c_comp" placeholder="請選擇公司" @change="get_data">
        <el-option :label="'請選擇公司'" :value="0"></el-option>
        <el-option v-for="(item,index) in comp" :label="item.CompanyName" :value="item.Id"></el-option>
	    </el-select>
  </div>
	  <div class="chart-main">
	    <canvas width="400" height="400" ref="c1"></canvas>
	  </div>
    <h2>返回區域比率</h2>
	  <div class="chart-main">
	    <canvas width="400" height="400" ref="c2"></canvas>
	  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	    list:[],
	    list2:[],
      ctxt:"",
      ctxt2:"",
      county:[],
      c_county:0,
      comp:[],
      c_comp:"",
      s_day:"",
      e_day:""
	  }
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
    await this.all_ready()
    this.county=await this.Function.get_GetTaiwanCounty_Id(189)
    this.comp=await this.Function.get_CarCompanys()
    this.c_comp=this.comp[0].Id
    await this.get_data()
	  this.GLOBAL.show_black=false
	  console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function(x) {
      this.GLOBAL.show_black=true
	  this.list=await this.Function.get_GetFromArea(this.s_day,this.e_day,this.c_comp,this.c_county).catch(e=>{alert("發生錯誤，請再試一次");this.GLOBAL.show_black=false})  
	  this.list2=await this.Function.get_GetBackrea(this.s_day,this.e_day,this.c_comp,this.c_county).catch(e=>{alert("發生錯誤，請再試一次");this.GLOBAL.show_black=false})  
            var arr_name = this.list.map(x => x.AreaName)
            var arr1 = this.list.map(x => x.RateOne)
            var arr2 = this.list.map(x => x.RateTwo)
            var arr_name_2 = this.list2.map(x => x.AreaName)
            var arr1_2 = this.list2.map(x => x.RateOne)
            var arr2_2 = this.list2.map(x => x.RateTwo)
            var arr_color = []
            for (let i in this.list) {
              arr_color.push(this.Function.r_color())
            }
            this.$nextTick(function () {
              var obj = [
                {
                  backgroundColor: arr_color,
                  data: arr1
                }
              ]
              var obj2 = [
                 {
                  backgroundColor: arr_color,
                  data: arr1_2
                }               
              ]
              if(this.ctxt) {
                this.ctxt.destroy()
              }
			  this.ctxt = this.Function.chart.drew_chart_remove_lab(this.$refs.c1, 'bar', arr_name, obj)
              if(this.ctxt2) {
                this.ctxt2.destroy()
              }
              this.ctxt2 = this.Function.chart.drew_chart_remove_lab(this.$refs.c2, 'bar', arr_name_2, obj2)
            })
            this.GLOBAL.show_black=false
	}
  }
}
</script>



