<template>
  <div class="dispatch" @click="munu_all_close()">
    <h2>{{GLOBAL.user.RoleId==34 ? "調度控制台":"調度單搜尋"}}</h2>
    <MBox :title="'車輛使用證明'" ref="mb3">
      <div style="width:800px;height:600px;overflow-y: scroll;">
        <el-button @click="print()">列印</el-button>
        <div class="caruse-d">
 			  <table class="caruse" border=1 v-for="(item,index) in caruse_list">
				  <tr>
					  <td colspan=1><b>車輛使用證明單</b></td>
					  <td colspan=2>{{item.DespatchNo}}</td>
				  </tr>
				  <tr>
					  <td><b>個案編號</b><br>{{item.CaseNo}}</td>
					  <td><b>乘客</b><br>{{item.CaseName}}</td>
					  <td><b>聯絡電話/手機</b><br>{{item.ContactPhone}}</td>
				  </tr>
				  <tr>
					  <td><b>起點</b><br>{{item.StartLocation}}</td>
					  <td>{{Function.moment.yyyymmdd(item.ReservationDate)}}<br>{{item.StartAddr}}</td>
					  <td>{{item.CaseUpTime ? Function.moment.yyyymmddhhmm(item.CaseUpTime) : ""}}</td>
				  </tr>
				  <tr>
					  <td><b>終點</b><br>{{item.EndLocation}}</td>
					  <td>地址<br>{{item.EndAddr}}</td>
					  <td>{{item.CasePayTime ? Function.moment.yyyymmddhhmm(item.CasePayTime) : ""}}</td>
				  </tr>
				  <tr>
					  <td colspan=1><b>司機</b><br>{{item.DriverName}}</td>
					  <td colspan=2><b>車輛</b><br>{{item.CarNo}}</td>
				  </tr>
				  <tr>
					  <td><b>應收金額</b><br>{{item.TotalAmt}}</td>
					  <td><b>實收金額</b><br>{{item.ReceiveAmt}}</td>
					  <td><b>待收金額</b><br>{{item.WaitReceive}}</td>
				  </tr>
				  <tr>
					  <td colspan=3><b>乘客或家屬簽名</b>
						  <div style="height:200px;"></div>
					  </td>
				  </tr>
			  </table>
        </div>   
      </div>
    </MBox>
    <MBox :title="'匯出車輛使用證明'" ref="mb1">
      <div style="width:300px;">
        <div style="padding:0 0 10px 0;">
          <el-input v-model="carp.no" placeholder="訂單編號"></el-input>
          <el-date-picker v-model="carp.data1" type="date" style="width:100%;" placeholder="選擇開始日期" value-format="yyyy-MM-dd"></el-date-picker>
          <el-date-picker v-model="carp.data2" type="date" style="width:100%;" placeholder="選擇結束日期" value-format="yyyy-MM-dd"></el-date-picker>
        </div>
        <div class="bt" @click="export_table()">確定提交</div>
      </div>
    </MBox>  
    <MBox :title="'變更司機'" ref="mb2">
      <div style="width:300px;">
        <div style="padding:0 0 10px 0;">
          <el-select v-model="c_driver.DriverInfoId">
            <el-option v-for="(item,index) in all_driver" :label="item.DriverName" :value="item.Id"></el-option>
          </el-select>
        </div>
        <div class="bt" @click="to_c_driver()">確定變更</div>
      </div>
    </MBox> 
    <MBox :title="'訂單詳細資料'">
      <div class="dispatch-mbox">
		  <div>
		  <h3>訂單資料</h3>
        <div v-if="!isd">
          <el-button @click="show_c_time=!show_c_time">變更時間</el-button>
          <div v-if="show_c_time" style="padding: 10px 0;">
            <el-select placeholder="請選擇時間" v-model="c_time" style="width:78%;">
              <el-option v-for="(item,index) in time" :label="item" :value="item"></el-option>
            </el-select>
	          <el-select v-model="c_comp" placeholder="請選擇公司" @change="get_data" v-if="GLOBAL.user.RoleId!=34">
              <el-option :label="'請選擇公司'" :value="0"></el-option>
              <el-option v-for="(item,index) in comp" :label="item.CompanyName" :value="item.Id"></el-option>
	          </el-select>
            <el-button style="width:20%;" @click="set_time()">確定</el-button>
          </div>
        </div>
        <div class="more-d" v-if="!isd"><span>訂單編號: {{c_order.SOrderNo}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>訂單編號: {{c_order.OrderDetails.SOrderNo}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>預約搭車時間: {{Function.moment.hhmm(c_order.ReservationDate)}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>預約搭車時間: {{Function.moment.hhmm(c_order.OrderDetails.ReservationDate)}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>是否回程: {{c_order.IsBack ? "是":"否"}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>是否回程: {{c_order.OrderDetails.IsBack ? "是":"否"}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>共乘: {{c_order.CanShared ? "是":"否"}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>共乘: {{c_order.OrderDetails.CanShared ? "是":"否"}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>輪椅類型: {{c_order.WheelchairId}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>輪椅類型: {{c_order.OrderDetails.WheelchairId}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>陪同人數: {{c_order.FamilyWith}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>陪同人數: {{c_order.OrderDetails.FamilyWith}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>起點: {{c_order.FromAddr}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>起點: {{c_order.OrderDetails.FromAddr}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>迄點: {{c_order.ToAddr}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>迄點: {{c_order.OrderDetails.ToAddr}}</span><hr></div>
	    </div>
		<div>
		  <h3>個案資料</h3>
		    <div class="more-d" v-if="!isd"><span>個案編號: {{c_order.CaseUserNo}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>個案編號: {{c_order.CaseUser.CaseUserNo}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>個案姓名: {{c_order.CaseUserName}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>個案姓名: {{c_order.CaseUser.Name}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>市話: {{c_order.CaseUserTel}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>市話: {{c_order.CaseUser.Tel}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>手機: {{c_order.CaseUserPhone}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>手機: {{c_order.CaseUser.Phone}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>身分證字號: {{c_order.CaseUserUID}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>身分證字號: {{c_order.CaseUser.UID}}</span><hr></div>
	    </div>
	  </div>
	</MBox>
    <div class="seach">
        <el-date-picker v-model="day" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
	      <el-select v-model="c_comp" placeholder="請選擇公司" @change="get_data" v-if="GLOBAL.user.RoleId!=34">
          <el-option :label="'請選擇公司'" :value="0"></el-option>
          <el-option v-for="(item,index) in comp" :label="item.CompanyName" :value="item.Id"></el-option>
	      </el-select>
        <el-button type="primary" icon="el-icon-document-checked" @click="put_data()" v-if="GLOBAL.user.RoleId==34">推播</el-button>
        <el-button @click="$refs.mb1.switch_open()">車輛使用證明單</el-button>
    </div>
	<div class="dispatch-order" v-if="GLOBAL.user.RoleId==34">
	  <div class="dispatch-order-d" v-for="(item,index) in order" draggable=true @dragstart="dragStart($event,item)" @dblclick="isd=false;c_order=item;MBox.switch_open()">
      <div>{{item.SOrderNo}} ({{item.CarCategoryId==236 ? "一般車":"福祉車"}})</div>
      <div>{{item.CaseUserName}} ({{item.SubsidyMode ? "公費":"自費"}})</div>
      <div>起: {{item.FromAddr}}</div>
      <div>迄: {{item.ToAddr}}</div>
      <div>{{Function.moment.hhmm(item.ReservationDate)+" / 預估"+item.ExpectedMinute+"分鐘"}}</div>
      <div>{{item.CanShared ? "可共乘":"不可共乘"}}</div>
	  </div>
	</div>
	<div class="dispatch-d" :class="{'dispatch-d-full':GLOBAL.user.RoleId!=34}">
    <table border="1" class="dispatch-table">
      <tr>
        <th>車輛\時間</th>
        <th v-for="(item,index) in time">{{item}}</th>
      </tr>
      <tr v-for="(item,index) in car" @drop.stop="post_des($event,item)" @dragover.stop="dragover($event)" @dragleave.stop="dragleave($event)">
        <th>{{item.CarNo}}<div>{{item.DriverInfo.DriverName}}</div></th>
        <td v-for="(item2,index2) in time">
          <div class="dispatch-table-d" v-for="(item3,index3) in dispatch" @dragover.stop="dragover($event)" @dragleave.stop="dragleave($event)" @drop.stop="with_order($event,item3)" v-if="item3.DespatchDetails[0].CarNo==item.CarNo&&check_time_h(item3.time,item2)">
			<div class="dispatch-table-d-d" v-for="(item4,index4) in item3.DespatchDetails" @contextmenu="menu_open($event,item4)" @dblclick="isd=true;c_order=item4;MBox.switch_open()">
			  <div class="dispatch-table-d-d-d" style="font-weight: bold;">{{item4.CaseUser.Name}} ({{item4.OrderDetails.ExpectedMinute}}分鐘)</div>
			  <div class="dispatch-table-d-d-d">{{item4.OrderDetails.ToAddr}}</div>
			  <i class="el-icon-close dispatch-table-d-d-x" @click="del_des(item4)"></i>
			  <div class="dispatch-table-d-d-menu" v-if="item4.open&&GLOBAL.user.RoleId==34">
			    <div class="dispatch-table-d-d-menu-d" @click="c_put_data(item3);item4.open=false;$forceUpdate();">推播此訂單</div>
				  <div class="dispatch-table-d-d-menu-d" @click="c_driver.Id=item3.DespatchId;c_driver.CarId=item4.Despatch.CarId;c_driver.DriverInfoId=item.DriverInfo.Id;$refs.mb2.switch_open()">變更司機</div>
				  <div class="dispatch-table-d-d-menu-d" @click="del_des(item4)">刪除訂單</div>
			  </div>
			</div>
		  </div>
        </td>
      </tr>
     </table>
	  <div class="dispatch-d-row" v-if="show_row">
		<div class="dispatch-d-row-d">車輛\時間</div>
	    <div class="dispatch-d-row-d" v-for="(item,index) in time">{{item}}</div>
	  </div>
	  <div class="dispatch-d-cell" v-if="show_cell">
	    <div class="dispatch-d-cell-d" v-for="(item,index) in car"><div>{{item.CarNo}}<br>{{item.DriverInfo.DriverName}}</div></div>
	  </div>
	</div>
  </div>
</template>

<script>
import moment from "moment";
import MBox from './MBox'
export default {
  data() {
    return {
      time:["05:00","06:00","07:00","08:00","09:00","10:00","11:00","12:00","13:00","14:00","15:00","16:00","17:00","18:00","19:00","20:00","21:00","22:00","23:00"], 
	    order: "",
	    c_order:"",
      dispatch: "",
      car: "",
      all_driver:"",
      caruse_list:"",
      isd:false,
      day: moment().format("YYYY-MM-DD"),
      show_c_time:false,
      c_time:"",
	    show_row:false,
      show_cell:false,
      comp:"",
      c_comp:"",
      dr_x:0,
      carp:{
        no:"",
        data1:"",
        data2:""
      },
      c_driver:{
				"Id": "",
				"DriverInfoId": "",
				"CarId": "",
      }
    };
  },
  components: {MBox},
  mounted: async function() {
    this.GLOBAL.show_black = true;
    await this.all_ready();
    if(this.GLOBAL.user.RoleId!=34) {
      this.comp=await this.Function.get_CarCompanys()
      this.c_comp=this.comp[1].Id
    }
    await this.get_data();
    this.all_driver=await this.Function.get_driverNoPage()
      var that=this
      document.querySelectorAll(".d1-r")[0].addEventListener("scroll",function() {
        if(that.GLOBAL.user.RoleId==34&&document.querySelectorAll(".dispatch-order")[0]) {
          var list=document.querySelectorAll(".dispatch-order")[0]
          if(this.scrollTop>=(263-80)) {
            list.style.top=this.scrollTop-(263-80)+"px"
          }
        }
        if(this.scrollTop>179) {
          that.show_row=true
          that.$nextTick(()=> {
            if(document.querySelectorAll(".dispatch-d-row")[0]) {
              document.querySelectorAll(".dispatch-d-row")[0].style.left=that.dr_x+"px"
              document.querySelectorAll(".dispatch-d-row")[0].style.top=this.scrollTop-142+"px"
            }
          })
        }
        else {
          that.show_row=false
        }
      }, false);
    this.GLOBAL.show_black = false;
  },
  watch: {
    async $route(to, from) {
      this.GLOBAL.show_black = true;
      await this.get_data();
      this.GLOBAL.show_black = false;
    }
  },
  methods: {
    get_data: async function(x) {
      this.GLOBAL.show_black=true
      if(this.GLOBAL.user.RoleId==34) this.order=await this.Function.get_dispatch_order(this.day)
	    this.dispatch=await this.Function.get_dispatch_allgroup(this.day,this.c_comp)
	    this.car=await this.Function.get_dispatch_car(this.c_comp).catch(e=>alert(e))
	    for(let i in this.dispatch) {
	      this.dispatch[i].time=moment(this.dispatch[i].DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm")
      }
	    for(let i in this.car) {
	      this.car[i].open=false
      }
      this.re_scroll()
      await this.Function.delay(0)
      this.$nextTick(()=> {
        document.querySelectorAll(".dispatch-d")[0].style.height=document.querySelectorAll(".dispatch-table")[0].offsetHeight+200+"px"
      })
      this.GLOBAL.show_black=false
	},
	show_c:function() {
      console.log(this.dispatch);
	},
	re_scroll:async function() {
	  var that=this
      var myScroll = new IScroll(document.querySelectorAll(".dispatch-d")[0], {
        scrollX: true,
		    scrollY: true,
		    probeType: 3,
        mouseWheel: false,
        disablePointer: true,
        disableTouch: false,
        disableMouse: false
	  })
	  myScroll.on('scroll', function () {
      if(this.x<=-205) {
        that.dr_x=this.x
        that.show_cell=true
        that.$forceUpdate()
        that.$nextTick(()=> {
          var row=document.querySelectorAll(".dispatch-d-row")[0]
          if(row) row.style.left=this.x+"px"
        })
		    that.re_cell()
		  }
		  else {
        that.show_cell=false
        that.$forceUpdate()
		  }
	  });
	},
	re_cell:function() {
	  var cell=document.querySelectorAll(".dispatch-d-cell-d")
	  var tr=document.querySelectorAll(".dispatch-table tr")
	  for(let i=0;i<cell.length;i++) {
		  cell[i].style.height=tr[i+1].offsetHeight+"px"
	  }
	},
	menu_open:function(e,x) {
	  e.preventDefault();
	  if(x.open) {
		x.open=false
		this.$forceUpdate()
		return 0
	  }
      for (let i in this.dispatch) {
	    for(let j in this.dispatch[i].DespatchDetails) {
          this.dispatch[i].DespatchDetails[j].open=false
		} 
	  }
	  x.open=!x.open
	  this.$forceUpdate()
  },
  munu_all_close:function() {
    for (let i in this.dispatch) {
	    for(let j in this.dispatch[i].DespatchDetails) {
        this.dispatch[i].DespatchDetails[j].open=false
		  } 
    }
    this.$forceUpdate()
  },
	dragStart:function(e,x) {
      e.dataTransfer.setData("Text", JSON.stringify(x));
	},
	dragover:function(e) {
      e.preventDefault();
      e.currentTarget.classList.add("dispatch-table-tr-on")
  },
  dragleave:function(e) {
    e.preventDefault();
    e.currentTarget.classList.remove("dispatch-table-tr-on")   
  },
  del_des:async function(x) {
    console.log(x)
    this.GLOBAL.show_black=true
    this.$message(await this.Function.del_Despatch(x.OrderDetails.Id))
    await this.get_data()
    this.GLOBAL.show_black=false
  },
  put_data:async function() {
    this.GLOBAL.show_black=true
    this.$message(await this.Function.put_Despatch(this.day))
    await this.get_data()
    this.GLOBAL.show_black=false
  },
	c_put_data:async function(x) {
		this.GLOBAL.show_black=true
    this.$message(await this.Function.put_PutDespatchById(x.DespatchId))
		this.GLOBAL.show_black=false
	},
  with_order:async function(e,x) {  //共乘
    this.GLOBAL.show_black=true
    var data = JSON.parse(e.dataTransfer.getData("Text"))
    var obj = {
			"Id": x.DespatchId,
			"DriverInfoId": 0,
			"CarId": 0,
			"OrderDetailId": data.Id
    }
    await this.Function.with_Despatch(obj)
    await this.get_data()
    this.GLOBAL.show_black=false
  },
  to_c_driver:async function() {
    this.GLOBAL.show_black=true
    this.$message(await this.Function.pDespatch(this.c_driver))
    await this.get_data()
    this.GLOBAL.show_black=false
  },
	export_table:async function() {
    this.GLOBAL.show_black=true
    this.caruse_list=await this.Function.get_GetCarsUse(this.carp.no,this.carp.data1,this.carp.data2)
    this.GLOBAL.show_black=false
    this.$refs.mb1.switch_open()
    this.$refs.mb3.switch_open()
  },
  check_time_h:function(x,y) {
    var flag=false
    if(x.split(":")[0]==y.split(":")[0]) {
      flag=true
    }
    return flag
  },
  print:function() {
    document.body.innerHTML=document.querySelectorAll(".caruse-d")[0].innerHTML
    window.print()
    location.reload()
  },
	post_des:async function(e,x) {
    this.GLOBAL.show_black=true
    e.preventDefault();
    this.dragleave(e)
	  var data = JSON.parse(e.dataTransfer.getData("Text"))
	  var obj = {
		  "DriverInfoId": x.DriverId,
		  "CarId": x.Id,
	    "StartDate": moment(data.ReservationDate).format("YYYY-MM-DD HH:mm"),
		  "DespatchDetail": [
		    { "OrderDetailId": data.Id }
		  ]
	  }	
    await this.Function.post_Despatch(obj)
    await this.get_data()
    this.GLOBAL.show_black=false
	}
  }
}
</script>


