<template>
  <div class="page" v-if="max">
    <!--<router-link v-for="(item,index) in max" :to="{ name:url, params:{page:item}, query:q}">
      <div class="page-d">{{item}}</div>
	  </router-link>-->
    <div class="page-d" @click="go_to(1)" v-if="$route.params.page>1"><<</div>
    <div class="page-d" @click="go_to(Number($route.params.page)-1)" v-if="$route.params.page>1"><</div>
    <select class="page-sel" v-model="$route.params.page" @change="go_to()">
      <option v-for="(item,index) in max" :value="item">{{item+"/"+max}}</option>
    </select>
    <div class="page-d" @click="go_to(Number($route.params.page)+1)" v-if="$route.params.page<max">></div>
    <div class="page-d" @click="go_to(max)" v-if="$route.params.page<max">>></div>
  </div>
</template>

<script>
import m from './../all.js'
export default {
  props: ['max','url','q','p'],
  data() {
    return {
	  list:[],
	}
  },
  mounted:async function() {
    this.sel_page()
  },
  watch:{
    $route (to, from){
      this.sel_page()
    }
  },
  methods:{
    sel_page:function() {
	    this.list=[]
	    var num=Number(this.$route.params.page)
	    var arr=[num-3,num-2,num-1,num,num+1,num+2,num+3]
	    for(let i in arr) {
	      if(arr[i]>0&&arr[i]<=this.max) {
		      this.list.push(arr[i])
		    }
	    }
    },
    go_to:function(x) {
      if(x) {
        this.$route.params.page=x
      }
      this.$router.push({ name:this.url ,params:{page:this.$route.params.page},query:this.q})
    }
  }
}
</script>



