<template>
  <div id="top">
    <el-row :gutter="20">
      <img src="./img/icon_mobile-menu.svg" @click="GLOBAL.left_open=!GLOBAL.left_open;GLOBAL.left_more_show=false">
      <el-col :xs="8" :sm="6" :md="4" :xl="6">
        <el-input class="hidden-xs-only" v-model="text" placeholder="快速搜尋訂單編號" prefix-icon="el-icon-search" @keyup.enter.native="go_search()"></el-input>
      </el-col>
    </el-row>
    <img class="top-logo" src="./img/logo-light.png">
    <div class="top-right hidden-xs-only">
      <div class="top-right-data">
        <div>
          <span v-if="GLOBAL.user.CarCompanys">{{GLOBAL.user.CarCompanys.CompanyName}}</span>
          <br>
          <span>{{GLOBAL.user.CompanyUserName}}</span>
        </div>
      </div>
      <div class="bt logout hidden-xs-only" @click="logout()">登出</div>
    </div>
  </div>
</template>

<script>
import m from './all.js'
export default {
  name: 'Top',
  data() {
    return {
	    text:""
	  }
  },
  mounted:async function() {
  },
  methods:{
    go_search:function() {
      this.$router.push({ name:"OrderList" ,params:{page:1} ,query:{status:"",pMode:"1",key:this.text} })
    },
    logout:function() {
      var r=confirm("確定登出?")
      if(!r) return 0
      localStorage.removeItem('dk3_user');
      location.href = './login2.html';
    }
  }
}
</script>



