<template>
  <div class="list">
    <h2>營運單位一覽表</h2>
    <div class="seach">
      <el-button type="primary" icon="el-icon-search" @click="is_edit=false;c_list=empty;MBox.switch_open()">新增</el-button>
    </div>
      <div class="list-d comp-list" v-for="(item,index) in list">
        <div><span>{{item.CompanyName}}</span></div><hr>
        <div class="comp-list-d">
          <div><span>負責人:</span>{{item.ChargeName}}</div>
          <div><span>手機:</span>{{item.ChargePhone}}</div>
          <div><span>市話:</span>{{item.ChargeTel}}</div>
          <div><span>Email:</span>{{item.ChargeEmail}}</div>
          <div><span>地址:</span>{{item.CompanyCounty+" "+item.CompanyDistrict+" "+item.CompanyAddr}}</div>
        </div>
        <div class="list-d-menu">
          <i class="el-icon-tickets" title="更多" @click="is_edit=true;c_list=item;MBox.switch_open()"></i>
        </div>
      </div>
	<div class="no-found" v-if="list&&list.length<1">查無資料</div>
  <MBox :title="'新增營運單位'">
    <div class="admin-list">
      <el-row>
        <el-col :span="8">公司名稱</el-col>
        <el-col :span="16"><el-input v-model="c_list.CompanyName"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">電子信箱</el-col>
        <el-col :span="16"><el-input v-model="c_list.ChargeEmail"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">公司代碼</el-col>
        <el-col :span="16"><el-input v-model="c_list.Id"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">負責人</el-col>
        <el-col :span="16"><el-input v-model="c_list.ChargeName"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">聯絡手機</el-col>
        <el-col :span="16"><el-input v-model="c_list.ChargePhone"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">連絡市話</el-col>
        <el-col :span="16"><el-input v-model="c_list.ChargeTel"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">統一編號</el-col>
        <el-col :span="16"><el-input v-model="c_list.EINNO"></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="8">地址</el-col>
        <el-col :span="16">
          <el-select v-model="c_list.CompanyCounty" placeholder="請選擇縣市">
            <el-option v-for="(item,index) in CitySel.city_sel.city_no" :key="index" :label="item" :value="item"></el-option>
          </el-select>  
          <el-select v-model="c_list.CompanyDistrict" placeholder="請選擇縣市">
            <el-option v-for="(item,index) in CitySel.city_sel[c_list.CompanyCounty]" :key="index" :label="item" :value="item"></el-option>
          </el-select>  
		      <el-input v-model="c_list.CompanyAddr"></el-input>
        </el-col>
      </el-row>
      <div class="bt" @click="sub()">確定提交</div>
    </div>
  </MBox>
  </div>
</template>

<script>
import Seach from './Seach'
import Page from './Page'
import MBox from './MBox'

export default {
  data() {
    return {
	    list:[],
      c_list:"",
      is_edit:false,
      empty: {
        "Id": 0,
        "ParentId": "",
        "Account": "",
        "Pwd": "",
        "CompanyName": "",
        "Logo": "",
        "EINNO": "",
        "ChargeName": "",
        "ChargeEmail": "",
        "ChargeTel": "",
        "ChargePhone": "",
        "CompanyCounty": "",
        "CompanyDistrict": "",
        "CompanyAddr": "",
        "IsDeleted": false,
        "Enabled": true,
      }
	  }
  },
  components: {Page,Seach,MBox},
  mounted:async function() {
    this.GLOBAL.show_black=true
	  await this.all_ready()
    await this.get_data()
	  this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
      await this.get_data()
	    this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function() {
	    this.list=await this.Function.get_CarCompanys()
	    console.log(this.list)
	  },
    del_data:async function(x) {
      var r = confirm("確定刪除?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.del_CarCompanys(x.Id))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    sub:async function() {
      this.GLOBAL.show_black=true
      if(this.is_edit) {
        alert(await this.Function.put_CarCompanys(this.c_list))
      }
      else {
        alert(await this.Function.post_CarCompanys(this.c_list))
      }
      await this.get_data()
      this.GLOBAL.show_black=false
    }
  }
}
</script>



