<template>
  <div class="list">
    <h2>司機車輛設定</h2>
      <div class="list-d type-list" v-for="(item,index) in list.data">{{item.CarTypeName}}
        <i :class="{'el-icon-arrow-down':!item.open,'el-icon-arrow-up':item.open}" @click="get_data_p(item.Id);all_close();item.open=true;obj.CarTypeName='',obj.ParentID=item.Id;$forceUpdate();"></i>
        <div class="list-d-more" v-if="item.open" @click.prevent="">
          <div class="list-d-more-d" v-for="(item2,index2) in list2.data">
            {{item2.CarTypeName}}
            <div class="list-d-more-d-menu">
              <i class="el-icon-circle-close list-d-more-d-del" @click="del_data(item2.Id)"></i>
            </div>
          </div>
        </div>
        <div v-if="item.open" @click.prevent="" style="display: flex;">
          <el-input v-model="obj.CarTypeName"></el-input>
          <el-button type="primary" icon="el-icon-circle-plus-outline" style="justify-content: flex-start; " @click="post_data()">新增</el-button>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list:[],
      list2:[],
      obj:{
        CarTypeName:"",
        ParentID:"",
        Sort:0
      }
	  }
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	  await this.all_ready()
    await this.get_data()
	  this.GLOBAL.show_black=false
  },
  methods:{
    get_data:async function() {
        this.list=await this.Function.get_CarsType()
        for(let i in this.list.data) {
          this.list.data[i].open=false
        }
	    console.log(this.list)
    },
    get_data_p:async function(x) {
      this.list2=[]
      this.list2=await this.Function.get_CarsType_parentID(x)
      console.log(this.list2)
    },
    del_data:async function(x) {
      this.GLOBAL.show_black=true
      alert(await this.Function.delete_CarsType_parentID(x))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    post_data:async function() {
      this.GLOBAL.show_black=true
      alert(await this.Function.post_CarsType_parentID(this.obj))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    all_close:function() {
      for(let i in this.list.data) {
        this.list.data[i].open=false
      }
    }
  }
}
</script>




