<template>
  <div class="chart">
    <h2>區域月報表</h2>
    <div class="seach">
        <el-date-picker v-model="day" type="month" placeholder="選擇年月" value-format="yyyy-MM" @change="get_data"></el-date-picker>
	      <el-select v-model="c_county" placeholder="請選擇地區" @change="get_data">
          <el-option :label="'請選擇地區'" :value="0"></el-option>
          <el-option v-for="(item,index) in county" :label="item.District" :value="item.Id"></el-option>
	      </el-select>
	      <el-select v-model="c_comp" placeholder="請選擇公司" @change="get_data">
          <el-option :label="'請選擇公司'" :value="0"></el-option>
          <el-option v-for="(item,index) in comp" :label="item.CompanyName" :value="item.Id"></el-option>
	      </el-select>
    </div>
	  <div class="chart-main">
	    <div class="chart-d" v-for="(item,index) in list">
	      <div class="chart-d-h">{{item.CarNo}}</div>
	      <canvas class="chart-c1" :style="{'width':'100%'}" :ref="'c'+index"></canvas>
	    </div>
	  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
    cv_list:[],
    county:[],
    c_county:0,
    comp:[],
    c_comp:"",
    day:"",
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
    await this.all_ready()
    this.county=await this.Function.get_GetTaiwanCounty_Id(189)
    this.comp=await this.Function.get_CarCompanys()
    this.c_comp=this.comp[0].Id
    await this.get_data()
	  this.GLOBAL.show_black=false
	  console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function(x) {
      this.GLOBAL.show_black=true
	  this.list=await this.Function.get_GetSingleCounty(this.c_county,this.day,this.c_comp).catch(e=>{alert("發生錯誤，請再試一次");this.GLOBAL.show_black=false})
    this.$nextTick(function () {
      for (let i in this.list) {
        var ctx = this.$refs["c"+i]
        var obj = [
          {
            label: this.list[i].County,
            data: [this.list[i].CompleteCount, this.list[i].MissCount, this.list[i].NotPerformedCount, this.list[i].TotalCount],
            backgroundColor: this.Function.r_color()
          }
        ]
        if(this.cv_list[i]) {
          this.cv_list[i].destroy()
        }
        this.cv_list[i]=this.Function.chart.drew_chart(ctx, 'horizontalBar', ["已完成"+" ("+this.list[i].CompleteCount+")", "空趟"+" ("+this.list[i].MissCount+")", "未執行"+" ("+this.list[i].NotPerformedCount+")", "總趟數"+" ("+this.list[i].TotalCount+")"], obj,true)
      }
    });
    this.GLOBAL.show_black=false
	}
  }
}
</script>



