      ],
      wheeltype: ["普通輪椅", "高背輪椅", "電動輪椅", "電動高背輪椅"],
      goback: false,
      stroke: [
        { name: "基隆市忠孝東路6段8號", time: "2019/05/11 18:38" },
        { name: "台北市復興南路6段8號", time: "2019/05/11 18:38" }
