  async mounted() {
    try {
      //檢查傳入參數this.$route.params.id
      let res = await this.Function.get_OrderDetails_more(this.OrderDetailsId)
console.log(res)
      //從這隻api才能獲得失能ID
      let res2 = await this.Function.get_case_more_edit(this.CaseUserId)
      let DisabilityId = res2.DisabilityId;

      this.temp_list = res.OrderDetails;

      this.list = {
        OrderId: this.temp_list.OrderId,
        CompanyId: this.temp_list.CompanyId,
        WheelchairId: this.temp_list.WheelchairId,
        ReservationDate: this.temp_list.ReservationDate,
        DisabilityId: DisabilityId,
        FromAddr: this.temp_list.FromAddr,
        FromAddrRemark: this.temp_list.FromAddr,
        ToAddr: this.temp_list.ToAddr,
        ToAddrRemark: this.temp_list.ToAddrRemark,
        OrderIdentity: this.temp_list.OrderIdentity,
        FamilyWith: this.temp_list.FamilyWith,
        ForeignFamilyWith: this.temp_list.ForeignFamilyWith,
        NeedLadder: false,
        CanShared: this.temp_list.CanShared,
        Remark: "",
        SubsidyMode: this.temp_list.SubsidyMode,
        CarCategoryId: this.temp_list.CarCategoryId,
        NoticePhone: this.temp_list.NoticePhone,
        OrderDetailsCompany: [{ CompanyId: this.temp_list.CompanyId, Sort: 1 }],
        IsBack: this.temp_list.IsBack
      };
      var OGReservationTime = Array.from(this.temp_list.ReservationDate);
      this.picked_date = OGReservationTime.slice(0, 10).join("");
      this.picked_hour = OGReservationTime.slice(11, 16).join("");
      this.list.ReservationDate = this.picked_date + " " + this.picked_hour;
    } catch (err) {
      console.log(err);
    }

    //取得車輛類別
    this.category =await this.Function.get_CarsType_parentID(8)
    this.list.CarCategoryId = this.category.data[0].Id;
    this.list_ready = true;
    //繪製地圖
    await this.initMap("from_addr", this.$refs.default_addr);
  },
  methods: {
    closeEdit() {
      this.$emit("closeEdit");
    },
    gobackHourHandler() {
      return (this.from_hr_index = this.hr.indexOf(this.picked_hour) + 1);
    },

    // 建立地圖
    async initMap(origin, inputAddr) {
      // console.log(origin,inputAddr)
      // 載入路線服務與路線顯示圖層
      // console.log(origin);
      // console.log(inputAddr);
      var directionsService = new google.maps.DirectionsService();
      var directionsDisplay = new google.maps.DirectionsRenderer();

      // 初始化地圖
      this.map = new google.maps.Map(
        document.getElementById("reservation_map"),
        {
          zoom: 16,
          center: { lat: 25.017302, lng: 121.539645 }
        }
      );

      // 放置路線圖層
      directionsDisplay.setMap(this.map);

      // 限制在台灣範圍
      let options = {
        componentRestrictions: { country: "tw" }
      };
      var autocomplete = new google.maps.places.Autocomplete(
        inputAddr,
        options
      );
      this.text_place = autocomplete.getPlace();

      // 地址的輸入框，值有變動時執行
      await autocomplete.addListener("place_changed", () => {
        // console.log("changed");
        if (origin == "from_addr") {
          let place = autocomplete.getPlace();
          this.map.setCenter({
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng()
          });
          // console.log("出發地址變更");

          this.list.FromAddr = inputAddr.value;
          this.showAmt();
        } else if (origin == "to_addr") {
          // console.log("回程地址變更");
          let place = autocomplete.getPlace();

          this.map.setCenter({
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng()
          });
          this.list.ToAddr = inputAddr.value;
          this.showAmt();
        }
      });
      await this.displayRoute(directionsService, directionsDisplay);
    },
    async displayRoute(directionsService, directionsDisplay) {
      this.from_where = "";
      if (this.list.FromLat && this.list.FromLon) {
        this.from_where = this.list.FromLat + "," + this.list.FromLon;
      } else {
        this.from_where = this.list.FromAddr;
      }

      var request = {
        origin: { query: this.list.FromAddr },
        destination: { query: this.list.ToAddr },
        provideRouteAlternatives: true,
        avoidTolls: true,
        travelMode: "DRIVING"
      };

      directionsService.route(request, (response, status) => {
        if (status === "OK") {
          // console.log("開始繪製路徑");
          directionsDisplay.setDirections(response);
        } else {
          // console.log("無法繪製路徑");
          this.form_confirmed = false;
        }
      });
    },
    //確認輸入的時間是否小於現在時間
    check_time(time) {
      return moment(time).isAfter(moment());
    },
    //根據輪椅種類修改CarCategoryId
    setCarIdByWheelType() {
      if (this.list.WheelchairId != "普通輪椅") {
        this.list.CarCategoryId = 214;
      }
    },
    async checkInput() {
      this.form_confirmed = true;
      if (this.list.FromAddrRemark == "其他") {
        if (this.other_remark_from == "") {
          alert("請輸入起點備註");
          this.form_confirmed = false;
        } else {
          this.list.FromAddrRemark = this.other_remark_from;
        }
      }

      if (this.list.ToAddrRemark == "其他") {
        if (this.other_remark_to == "") {
          alert("請輸入訖點備註");
          this.form_confirmed = false;
        } else {
          this.list.ToAddrRemark = this.other_remark_to;
        }
      }

      if (this.picked_date == "") {
        alert("未選擇預約日期");
        this.form_confirmed = false;
      } else if (
        this.picked_hour == "請選擇預約時間" ||
        this.picked_hour == ""
      ) {
        alert("未選擇預約時間");
        this.form_confirmed = false;
      } else if (this.list.FromAddr == "") {
        alert("未輸入上車地點");
        this.form_confirmed = false;
      } else if (this.list.ToAddr == "") {
        alert("未輸入下車地點");
        this.form_confirmed = false;
      } else if (this.list.NoticePhone != "") {
        if (!this.checkPhone(this.list.NoticePhone)) {
          alert("聯絡人簡訊格式不符");
          this.form_confirmed = false;
        }
      } else if (this.list.OrderIdentity == "") {
        alert("未選擇訂車人身分");
        this.form_confirmed = false;
      } else if (this.list.FromAddr == this.list.ToAddr) {
        alert("起訖點不能相同");
        this.form_confirmed = false;
      }
    },

    async nextPlace() {
      this.clicked = true;

      if (!this.form_confirmed) {
        this.clicked = false;
        return 0;
      } else {
        var order = {
          OrderId: 0, //主單編號
          ReservationDate: "", //預約時間
          ExpectedMinute: 0, //預估時間
          FromAddr: this.list.FromAddr, //出發地址
          FromAddrRemark: "",
          ToAddr: this.list.ToAddr, //目的地址
          ToAddrRemark: "",
          OrderIdentity: "",
          FamilyWith: 0, //陪同家人
          ForeignFamilyWith: 0, //陪同外籍
          NeedLadder: false, //是否需要爬梯
          CanShared: false, //是否願意共乘
          Remark: "string", //備註
          WheelchairId: "", //輪椅類型
          NoticePhone: "",
          CarCategoryId: this.list.CarCategoryId,
          OrderDetailsCompany: []
        };

        this.list.ReservationDate = this.picked_date + " " + this.picked_hour;

        // await this.submitFirstOrder();
        // OrderDetails/GetOrderDetailMappingView/17268
        //先取消訂單     /api/OrderDetails/PutDetailStatus?OrderDetailId=${id}&StatusInt=8
        await this.deleteOrder();
        await this.submitSecondOrder();
        await this.submitFinalHandler();
      }
    },
    async deleteOrder() {
      let res = await Axios.put(
        `/api/OrderDetails/PutDetailStatus?OrderDetailId=${this.OrderDetailsId}&StatusInt=9`
      );
      if (res.data.msg == "更新成功") {
        alert("開始更新訂單");
        console.log(res.data);
      } else {
        alert("訂單更新失敗");
      }
    },
    submitFinalHandler() {
      //如果submit_status是true (成功發佈的話)
      if (this.submit_status) {
        this.$router.history.go(-1);
      } else {
        alert("預約失敗，請重新輸入訂單後提交，謝謝。");
        this.$forceUpdate();
      }
    },
    //下訂子訂單
    async submitSecondOrder() {
      await Axios.post("/api/OrderDetails/Post", this.list)
        .then(res => {
          if (res.data.msg === "添加成功") {
            localStorage.save_list = JSON.stringify({
              id: this.list.CaseUserId,
              path: this.list,
              oid: this.list.OrderId
            });
            alert("訂單添加成功!");
            this.submit_status = true;
          } else if (res.data.msg === "網路錯誤，請再確認") {
            alert("網路錯誤，請再確認");
            this.submit_status = false;
            // this.submitSecondOrder();
          } else {
            alert("其他錯誤");
            console.log(res);
            this.submit_status = false;
          }
        })
        .catch(err => {
          if (
            err.data.Message === "Invalid operation. The connection is closed."
          ) {
            this.submitSecondOrder();
          } else {
            alert(`CATCH : ${err}`);
          }
        });
    },
    //顯示金額和距離
    async showAmt() {
      if (this.list.FromAddr != "" && this.list.ToAddr != "") {
        Axios.get(
          `/api/OrderDetails/GetOrderAmt/${this.CaseUserId},${this.list.FromAddr},${this.list.ToAddr},${this.list.CarCategoryId},${this.list.FamilyWith}?_canShared=${this.list.CanShared}&hasback=${this.list.IsBack}`
        ).then(res => {
          if (res.data.msg == "獲取成功") {
            this.Amt = res.data.response;
            this.list.SubsidyMode = this.Amt[0].SubsidyMode;
            this.list.SubsidyAmt = this.Amt[0].SubsidyAmt;
            this.list.SelfPayAmt = this.Amt[0].SelfPayAmt;
            this.list.OtherAmt = this.Amt[0].OtherAmt;
            this.list.TotalAmt = this.Amt[0].TotalAmt;
            this.list.ExpectedMinute = this.Amt[0].ExpectedMinute;
            this.list.TotalMileage = this.Amt[0].TotalMileage;
            this.list.HighwayMileage = this.Amt[0].HighwayMileage;
          } else {
            console.log(res.data.msg);
            this.Amt = res.data.response;
          }
        });
      }
    },
    checkPhone(phonenumber) {
      let bl = /^09\d{8}$/.test(phonenumber);
      return bl;
    },
    checkTime(item) {
      return moment(item).isAfter(moment());
    }
  }
};
</script>
<style scoped>
  @import url('../css/EditDespatchDetail.css');
</style>


