<template>
  <div class="chart">
  <h2>營收報表</h2>
  <div class="seach">
      <el-date-picker v-model="s_day" type="date" placeholder="選擇日期區間(起)" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
      <el-date-picker v-model="e_day" type="date" placeholder="選擇日期區間(迄)" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
	    <el-select v-model="c_county" placeholder="請選擇地區" @change="get_data">
        <el-option :label="'請選擇地區'" :value="0"></el-option>
        <el-option v-for="(item,index) in county" :label="item.District" :value="item.Id"></el-option>
	    </el-select>
	    <el-select v-model="c_comp" placeholder="請選擇公司" @change="get_data">
        <el-option :label="'請選擇公司'" :value="0"></el-option>
        <el-option v-for="(item,index) in comp" :label="item.CompanyName" :value="item.Id"></el-option>
	    </el-select>
  </div>
	<div class="chart-main">
	  <canvas width="400" height="400" ref="c_main"></canvas>
	</div>
  <h2>司機單月營收</h2>
	<div class="chart-b" v-if="list&&list.length>0">
	  <div class="chart-d" v-for="(item,index) in list">
	    <div class="chart-d-h">{{item.DriverName}}</div>
	    <canvas class="chart-c1" :ref="'c'+index"></canvas>
	  </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	    list:[],
      ctxt:"",
      county:[],
      c_county:0,
      comp:[],
      c_comp:"",
      s_day:"",
      e_day:""
	  }
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
    await this.all_ready()
    this.county=await this.Function.get_GetTaiwanCounty_Id(189)
    this.comp=await this.Function.get_CarCompanys()
    this.c_comp=this.comp[0].Id
    await this.get_data()
	  this.GLOBAL.show_black=false
	  console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function() {
    this.GLOBAL.show_black=true
	  this.list=await this.Function.get_GetDriverRevenue(this.s_day,this.e_day,this.c_county,this.c_comp).catch(e=>{alert("發生錯誤，請再試一次");this.GLOBAL.show_black=false})  
	  console.log(this.list)
	  var arr_to=this.list.map(x=>x.TotalRevenues)
	  var arr_se=this.list.map(x=>x.SelfRevenues)
	  var arr_su=this.list.map(x=>x.SubsidyRevenues)
      var arr_too=[0,0,0]
      for(let i in arr_to) {
        arr_too[0]+=arr_to[i]
        arr_too[1]+=arr_se[i]
        arr_too[2]+=arr_su[i]
      }
	  var arr_name=this.list.map(x=>x.DriverName)
	  this.$nextTick(function () {
	    var obj2=[
	      {
		    label: [],
		    data:[arr_too[0],arr_too[1],arr_too[2]],
		    backgroundColor: ['rgb(255, 99, 132)','rgb(255, 213, 0)','rgb(0, 140, 255)']
		  },
        ]
        if(this.ctxt) {
          this.ctxt.destroy()
        }
	    this.ctxt=this.Function.chart.drew_chart(this.$refs.c_main,'bar',['總營收','個案負擔','政府補助'],obj2,false)
			  
        for(let i=0;i<this.list.length;i++) {
          var ctx = this.$refs["c"+i]
	      var obj=[
            {
              data: [this.list[i].SelfRevenues,this.list[i].SubsidyRevenues],
              backgroundColor: ['rgb(255, 99, 132)',"rgba(0,0,0,0.1)"],
            }
          ]
          this.Function.chart.drew_chart(ctx,'doughnut',["個案負擔","政府補助"],obj,true)
	    }
    })
    this.GLOBAL.show_black=false
	}
  }
}
</script>



