<template>
  <div class="list">
    <h2>車輛資料</h2>
    <div class="seach">
        <el-input placeholder="輸入關鍵字" v-model="seach.key" prefix-icon="el-icon-search"></el-input>
	      <el-select v-model="seach.Status" placeholder="請選擇狀態">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
	      </el-select>
	      <el-select v-model="seach.carType" placeholder="請選擇車種">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value"></el-option>
	      </el-select>
	      <el-select v-model="seach.compId" placeholder="請選擇公司" v-if="comp">
          <el-option :label="'請選擇公司'" :value="''"></el-option>
          <el-option v-for="(item,index) in comp" :key="index" :label="item.CompanyName" :value="item.Id"></el-option>
	      </el-select>
        <el-button type="primary" icon="el-icon-search" @click="show_c">搜尋</el-button>
        <router-link :to="'/CarMore/'"><el-button type="primary" icon="el-icon-circle-plus-outline">新增</el-button></router-link>
        <a href="./download/%E5%8F%B8%E6%A9%9F%E8%BB%8A%E8%BC%9B%E5%8C%AF%E5%85%A5%E7%AF%84%E4%BE%8B.xlsx" style="float:right;" :class="'import-bt'" download><el-button type="primary">下載匯入範本</el-button></a>
        <el-upload :class="'import-bt'" :action="GLOBAL.up_xls" :show-file-list="false" :on-success="upload_xls_ok" :on-progress="upload_xls_loading" accept=".xls">
          <el-button type="primary">匯入</el-button>
        </el-upload>
    </div>
      <div class="list-d case-list list-title">
         <el-row>
          <el-col :xl="3" :md="8" :sm="8" :xs="8">照片</el-col>
          <el-col :xl="3" :md="8" :sm="8" :xs="8">車牌號碼</el-col>
          <el-col :xl="3" class="hidden-md-and-down">輪椅數量</el-col>
          <el-col :xl="3" class="hidden-md-and-down">座椅數量</el-col>
          <el-col :xl="3" class="hidden-md-and-down">車頂</el-col>
          <el-col :xl="3" class="hidden-md-and-down">廠牌型號</el-col>
          <el-col :xl="3" :md="8" :sm="8" :xs="8">車種</el-col>
        </el-row>
      </div>
      <div class="list-d case-list" v-for="(item,index) in list.data">
         <el-row>
          <el-col :xl="3" :md="8" :sm="8" :xs="8"><img class="list-d-img" :src="item.CarPic ? item.CarPic : 'https://api.donkeymove.com//UploadFolder/images/default.png'"></el-col>
          <el-col :xl="3" :md="8" :sm="8" :xs="8">{{item.CarNo}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{item.WheelchairNum}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{item.SeatNum}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{item.CarTop==225 ? "正常" : "高頂"}}</el-col>
          <el-col :xl="3" class="hidden-md-and-down">{{item.FactoryType}}</el-col>
          <el-col :xl="3" :md="8" :sm="8" :xs="8" v-if="item.CarCategory&&item.CarCategory[0]">{{item.CarCategory[0].CarTypeName}}</el-col>
        </el-row>       
        <div class="list-d-menu">
          <router-link :to="'/CarMore/'+item.Id"><i class="el-icon-tickets" title="更多"></i></router-link>
          <i class="el-icon-delete hidden-md-and-down" title="刪除" @click="del(item)" v-if="GLOBAL.user.RoleId!=34"></i>
        </div>
      </div>
	<div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'CarList'" :q="seach"/>
  </div>
</template>

<script>
import Seach from './Seach'
import Page from './Page'
export default {
  data() {
    return {
	  list:[],
    options:[
      {value: "",label: "全部"},
      {value: "true",label: "可派發"},
      {value: "false",label: "不可派發"},
    ],
    options2:[
      {value: "0",label: "全部車種"},
      {value: "236",label: "一般車"},
      {value: "214",label: "福祉車"},
    ],
    comp:"",
    seach:this.Function.p_json(this.$route.query)
	}
  },
  components: {Page,Seach},
  mounted:async function() {
    this.GLOBAL.show_black=true
    await this.all_ready()
    this.comp=await this.Function.get_CarCompanys()
    await this.get_data()
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data()
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function() {
	    this.list=await this.Function.get_car(this.$route.params.page,encodeURI(this.seach.key),this.seach.Status,this.seach.carType,this.seach.compId)
	    console.log(this.list)
	  },
    show_c:async function() {
      console.log(this.$route)
      this.$router.push({ name:"CarList" ,params:{page:1} ,query: this.seach})
    },
	  upload_xls_ok:async function(res) {
	    this.$message(res.msg)
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    upload_xls_loading:function() {
      this.GLOBAL.show_black=true
    },
  }
}
</script>




