<template>
  <div>
     <h3>可用補助</h3>
     <el-row><el-col :span="4">總額度</el-col><el-col :span="20">{{list.CaseUser.CaseUserCoupon.TotalCouponAmt}}</el-col></el-row>
     <el-row><el-col :span="4">使用額度</el-col><el-col :span="20">{{Math.abs(list.CaseUser.CaseUserCoupon.UseCouponAmt)}}</el-col></el-row>
     <el-row><el-col :span="4">剩餘額度</el-col><el-col :span="20">{{list.CaseUser.CaseUserCoupon.LastCouponAmt}}</el-col></el-row>
     <el-row><el-col :span="4">總趟次</el-col><el-col :span="20">{{coup.TotalDetailCount}}</el-col></el-row>
     <el-row><el-col :span="4">每月預設補助</el-col><el-col :span="20">{{coup.DefaultCoupon}}</el-col></el-row>
     <el-row><el-col :span="4">本月空趟次數</el-col><el-col :span="20">{{coup.MissBoardingMonth}}</el-col></el-row>
     <el-row><el-col :span="4">總共空趟次數</el-col><el-col :span="20">{{coup.MissBoarding}}</el-col></el-row>
     <el-row><el-col :span="4">本月自付額</el-col><el-col :span="20">{{coup.SelfPayMonth}}</el-col></el-row>
     <h3>修改紀錄</h3>
     <el-table :data="coup.CouponHistoryViewModel" style="width: 100%">
       <el-table-column prop="SetBy" label="原因" width="180"></el-table-column>
       <el-table-column prop="SetCount" label="金額" width="180"></el-table-column>
       <el-table-column label="日期">
			  <template slot-scope="scope">
          {{scope.row.SetDate ? Function.moment.yyyymmdd(scope.row.SetDate) : "無"}}
        </template>
       </el-table-column>
     </el-table>
  </div>
</template>

<script>
export default {
  props:["list","coup"],
  data() {
    return {
	  }
  },
  mounted:async function() {

  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{

  }
}
</script>



