<template>
  <div class="show-list">
    <el-col v-for="(item,index) in list_key">
      <el-input :placeholder="list[item].title" v-model="list[item].value" v-if="list[item].type=='input_text'"></el-input>
	    
      <el-select v-model="list[item].value" :placeholder="list[item].title" v-if="list[item].type=='select'">
        <el-option v-for="(item2,index2) in list[item].list" :key="item2.value" :label="item2.title" :value="item2.value"></el-option>
	    </el-select>

      <el-select v-model="list[item].value" multiple :placeholder="list[item].title" v-if="list[item].type=='select_multiple'">
        <el-option v-for="(item2,index2) in list[item].list" :key="item2.value" :label="item2.title" :value="item2.value"></el-option>
	    </el-select>
    
      <el-date-picker v-model="list[item].value" type="date" :placeholder="list[item].title" v-if="list[item].type=='date'">
    </el-date-picker>
    </el-col>

      <el-select v-model="list_m_v" multiple @change="show_list">
        <el-option v-for="(item,index) in list_m" :key="index" :label="item.name" :value="item.value"></el-option>
	    </el-select>
  </div>
</template>

<script>
export default {
  props: ['x'],
  data() {
    return {
      list:{
        d1:{title:"姓名",value:"小明",type:"label"},
        d2:{title:"編號",value:"TS123456789",type:"input_text"},
        d3:{title:"性別",value:1,type:"select",list:[
          {title:"男",value:1},
          {title:"女",value:2},
        ]},
        d4:{title:"日期",value:"",type:"date"},
        d5:{title:"核定服務",value:"",type:"select_multiple",list:[
          {title:"服務1",value:1},
          {title:"服務2",value:2},
          {title:"服務3",value:3},
        ]},
      },
      list_key:[],
      list_m_v:[2],
      list_m:[
        {name:111,value:1},
        {name:222,value:2},
        {name:333,value:3},
        {name:444,value:4},
        {name:555,value:5},
      ]
	}
  },
  mounted:async function() {
    this.list_key=Object.keys(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    go_to:function() {
	    router.push({ path: 'DriverList/1'})
	    location.reload()
    },
    show_list:function() {
      console.log(this.list_m_v)
    }
  }
}
</script>



