<template>
  <div class="seach">
    <el-col :span="4">
      <el-input placeholder="輸入關鍵字" v-model="GLOBAL.seach.key" prefix-icon="el-icon-search"></el-input>
    </el-col>
	<el-col :span="4">
	  <el-date-picker v-model="GLOBAL.seach.star_data" type="date" placeholder="選擇日期"></el-date-picker>
	</el-col>
	<el-col :span="4" v-if="inp">
	  <el-select v-model="sel" placeholder="請選擇縣市" v-if="inp.sel">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
	  </el-select>
	</el-col>
  </el-select>
	
	<button @click="go_to">OK</button>
  </div>
</template>

<script>
import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
const router = new VueRouter({})
export default {
  props: ['call','inp','url','arr'],
  data() {
    return {
        options: [{
          value: '111',
          label: '111'
        }, {
          value: '222',
          label: '222'
        }, {
          value: '333',
          label: '333'
        }, {
          value: '444',
          label: '444'
        }, {
          value: '555',
          label: '555'
        }],
		sel:"",
		to_url:""
	}
  },
  mounted:async function() {

  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    go_to:function() {
	  router.push({ path: 'DriverList/1'})
	  location.reload()
	}
  }
}
</script>



