<template>
  <div class="left" :class="{'left-open':GLOBAL.left_open}">
	<input type="file" ref="file1" name="avatar" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="import_data($event,Function.import_data_driver)" style="display:none;" /> 
      <div class="left-d">
	    <div class="left-d-d" :class="{'left-d-d-on':item.on}" v-for="(item,index) in list" @click="change_list(index)" v-if="is_inmenu(item.no)!==-1">
		  <i class="left-d-d-icon" :class="item.img"></i>
		  <div class="left-d-d-d">{{item.title}}</div>
	    </div>
		<div class="left-user-data-b">
		  <div class="left-user-data">
		    <div v-if="GLOBAL.user.CarCompanys">{{GLOBAL.user.CarCompanys.CompanyName}}</div>
		    <div>{{GLOBAL.user.CompanyUserName}}</div>
		  </div>
		  <div class="bt left-logout" @click="logout()">登出</div>
        </div>
	  </div>
	  <div class="left-d-more" :class="{'left-d-more-show':GLOBAL.left_more_show}">
	  <router-link v-for="(item,index) in list[c_list].c_title" :to="item.url" v-if="item.url&&is_inmenu(item.no)!==-1&&!item.bg">
	    <div class="left-d-more-d" :class="{'left-d-more-d-on':item.on}" @click="is_on(item,list[c_list])">{{item.title}}</div>
	  </router-link>
	  <a :href="item.download" v-for="(item,index) in list[c_list].c_title" v-if="item.bg"><img class="left-d-more-img" :src="item.bg"></a>
	  <div class="left-d-more-d" @click="GLOBAL.left_more_show=!GLOBAL.left_more_show"><i class="el-icon-d-arrow-left"></i></div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  c_list:0,
	  list:[
	    {title:"控制台",url:"/Chart",img:"el-icon-data-analysis",no:1,on:false,c_title:[
		  {title:"本日接送狀況",url:"/Chart1",img:"",no:2,on:false},
		  {title:"車輛使用率比較表",url:"/Chart2",img:"",no:3,on:false},
		  {title:"營收報表",img:"",url:"/Chart3",no:4,on:false},
		  {title:"出發&返回區域比率",url:"/Chart4",img:"",no:5,on:false},
		  {title:"區域月報表",url:"/Chart5",img:"",no:6,on:false},
		  {title:"醫療院所比例",url:"/Chart6",img:"",no:38,on:false},
		]},
		{title:"預約訂單",url:"/OrderList",img:"el-icon-document",no:8,on:false,c_title:[
		  {title:"全部資料",url:"/OrderList/1?status=&pMode=1&key=&time=",img:"",no:9,on:false},
		  {title:"今日任務",url:"/OrderList/1?status=&pMode=2&key=&time=",img:"",no:10,on:false},
		  {title:"本週任務",url:"/OrderList/1?status=&pMode=3&key=&time=",img:"",no:11,on:false},
		  {title:"本月任務",url:"/OrderList/1?status=&pMode=4&key=&time=",img:"",no:12,on:false},
		  {title:"今年任務",url:"/OrderList/1?status=&pMode=5&key=&time=",img:"",no:13,on:false},
		]},
		{title:"派車調度",url:"/CaseList/",img:"el-icon-truck",no:14,on:false,c_title:[
		  {title:"調度單搜尋",url:"/DragDispatch/",img:"",no:15,on:false},
		  {title:"調度控制台",url:"/DragDispatch/",img:"",no:16,on:false},
		  {title:"調度控制台-表格式",url:"/TableDispatch/",img:"",no:40,on:false},
		]},
		{title:"個案管理",url:"/CaseList",img:"el-icon-user",no:17,on:false,c_title:[
		  {title:"全部個案",url:"/CaseList/1?key=&pMode=1",img:"",no:18,on:false},
		  {title:"自費個案",url:"/CaseList/1?key=&pMode=3",img:"",no:20,on:false},
		  {title:"公費個案",url:"/CaseList/1?key=&pMode=4",img:"",no:21,on:false},
		  {title:"結案",url:"/CaseList/1?key=&pMode=5",img:"",no:22,on:false},
		]},
		{title:"司機車輛管理",url:"/DriverList/",img:"el-icon-date",no:23,on:false,c_title:[
		  {title:"駕駛資料",url:"/DriverList/1?key=&status=&compId=",img:"",no:27,on:false},
		  {title:"車輛資料",url:"/CarList/1?key=&Status=&carType=0&compId=",img:"",no:24,on:false},
		]},
		{title:"系統資料管理",url:"/CaseList/",img:"el-icon-setting",no:29,on:false,c_title:[
		  {title:"輔助車資參數",url:"/AreaSubsidy/",img:"",no:30,on:false},
		  {title:"司機車輛設定",url:"/CarsTypeList/",img:"",no:31,on:false},
		  {title:"營運單位一覽表",url:"/CompanyList",img:"",no:32,on:false},
		  {title:"A單位一覽表",url:"/ACompanyList/1",img:"",no:37,on:false},
		  {title:"核定地點管理",url:"/AddrTurnNameList/1",img:"",no:33,on:false},
		  {title:"系統管理員設定",url:"/AdminList/1",img:"",no:34,on:false},
		]},
		/*{title:"匯入",url:false,img:"el-icon-upload2",no:1,on:false,c_title:[
		  {title:"匯入司機/車輛",url:false,img:"",no:1,open_input:"file1",on:false},
		]},
		{title:"匯出",url:false,img:"el-icon-download",no:1,on:false,c_title:[
		  {title:"匯出服務紀錄",url:false,img:"",no:1,on:false},
		  {title:"匯出任務明細檔",url:false,img:"",no:1,on:false},
		]},*/
		{title:"其他",url:false,img:"el-icon-more",no:1,on:false,c_title:[
		  {title:"常見問題Q&A",url:"/QA",img:"",no:1,on:false},
		  {title:"Android版司機端APP下載",url:"/download",img:"",no:1,on:false,bg:"./img/android_download.png",download:"./download/NTPC_1.0_20191226.apk"},
		  {title:"IOS版司機端APP下載",url:"/download",img:"",no:1,on:false,bg:"./img/ios_download.png",download:"#"},
		]},
	  ]
	}
  },
  mounted:async function() {
    console.log(this.$route)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.left_open=false
	  this.GLOBAL.left_more_show=false
      console.log(this.$route)
    }
  },
  methods:{
    get_path:function(x) {
	  var arr=x.split("/")
	  return arr
	},
	change_list:function(x) {
	  this.GLOBAL.left_more_show=true
	  this.c_list=x;
	},
    reload:function() {
	  m.reload()
	},
	show_note:function() {
	  var data=moment().format('HH:mm:ss')
      this.$notify({
        title: '收到新訂單',
        dangerouslyUseHTMLString: true,
		duration:10000,
        message: '<div">訂單編號:TS5555123<br>'+data+'</div>'
      });
	},
	import_data:async function(e,callback) {
	  this.GLOBAL.show_black=true
      let file=e.target.files[0]
      let formData = new FormData();
      formData.append('file', file);
	  alert(await callback(formData))
	  this.GLOBAL.show_black=false
	},
	is_on:function(x,y) {
	  for(let item of this.list) {
		item.on=false
		for(let item2 of item.c_title) {
          item2.on=false
		}
	  }
	  x.on=true
	  y.on=true
	},
	is_inmenu:function(x) {
      return this.GLOBAL.menu.indexOf(Number(x))
	},
    logout:function() {
      var r=confirm("確定登出?")
      if(!r) return 0
      localStorage.removeItem('dk3_user');
      location.href = './login2.html';
    }
  }
}
</script>


