<template>
  <div class="list">
    <h2>全部訂單</h2>
    <MBox :title="'刪除訂單'">
      <div class="admin-list">
        <h3>目前累計違規點數 {{c_list.TotalViolationCount}}點</h3>
        <div style="margin:0 0 20px 0;">
          <ol style="color:#f00;">
            <li>乘客預約時間十分鐘後，仍未抵達約定乘車地點，並視同放棄當次服務，駕駛員回報中心並離開去服務下位乘客，記違規1點。</li>
            <li>乘車日前(48小時)未申請更改或取消服務者，記違規1點。</li>
            <li>累積違規3點後，停止服務2週，懲處完畢後歸0，重新累積記點規範。</li>
          </ol>
	        <el-select v-model="del_order.point" placeholder="請選擇扣點點數">
            <el-option :label="'0點'" :value="0"></el-option>
            <el-option :label="'1點'" :value="1"></el-option>
            <el-option :label="'2點'" :value="2"></el-option>
            <el-option :label="'3點'" :value="3"></el-option>
            <el-option :label="'4點'" :value="4"></el-option>
            <el-option :label="'5點'" :value="5"></el-option>
	        </el-select>
          <el-input v-model="del_order.remark" placeholder="扣點理由"></el-input>
        </div>
        <div class="bt" @click="to_del()">確定刪除</div>
      </div>
    </MBox>
    <div class="seach">
        <el-input placeholder="輸入關鍵字" v-model="seach.key" prefix-icon="el-icon-search" @keyup.enter.native="show_c()"></el-input>
	      <el-date-picker v-model="seach.time" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd"></el-date-picker>
        <el-select v-model="seach.pMode" placeholder="請選擇">
          <el-option :label="'全部任務'" :value="'1'"></el-option>
          <el-option :label="'今日任務'" :value="'2'"></el-option>
          <el-option :label="'本周任務'" :value="'3'"></el-option>
          <el-option :label="'本月任務'" :value="'4'"></el-option>
          <el-option :label="'今年任務'" :value="'5'"></el-option>
	      </el-select>
	      <el-select v-model="seach.status" placeholder="請選擇狀態">
          <el-option :label="'全部狀態'" :value="''"></el-option>
          <el-option :label="'新訂單'" :value="0"></el-option>
          <el-option :label="'已排班'" :value="1"></el-option>
          <el-option :label="'已出發'" :value="2"></el-option>
          <el-option :label="'已抵達'" :value="3"></el-option>
          <el-option :label="'客上'" :value="4"></el-option>
          <el-option :label="'未執行'" :value="7"></el-option>
          <el-option :label="'客戶取消'" :value="8"></el-option>
          <el-option :label="'服務單位取消'" :value="9"></el-option>
          <el-option :label="'空趟'" :value="10"></el-option>
          <el-option :label="'已完成'" :value="6"></el-option>
	      </el-select>
        <el-button type="primary" icon="el-icon-search" @click="show_c()">搜尋</el-button>
        <el-button type="primary" @click="ExportTable.switch_open('GetServiceRecord')">服務紀錄</el-button>
        <el-button type="primary" @click="ExportTable.switch_open('GetPickMission')">任務明細檔</el-button>
    </div>
      <div class="list-d order-list" v-for="(item,index) in list.data">
        <div class="order-list-d1">
          <div style="width:100%;">
            <div class="order-list-d1-d">訂單編號:{{item.SOrderNo}}</div><hr>
            <div class="order-list-d1-d">個案姓名:{{item.CaseUserName}}</div>
            <div class="order-list-d1-d">預約時間:{{Function.moment.yyyymmdd(item.ReservationDate)}}</div>
            <div class="order-list-d1-d">狀態:{{GLOBAL.status_arr[item.Status]}}</div>
            <div class="order-list-d1-d">行程:{{item.IsBack ? "回程" : "去程"}}</div>
            <div class="order-list-d1-d">建立時間:{{Function.moment.yyyymmdd(item.CreateTime)}}</div>
          </div>        
        </div>
        <div class="order-list-d2">
          <div style="width:100%;">
            <div class="order-list-d3-d">{{item.CanShared ? "可共乘" : "不可共乘"}}</div>
            <div class="order-list-d3-d">{{item.FromAddr}}</div>
            <div class="order-list-d3-d"><i class="el-icon-bottom"></i></div>
            <div class="order-list-d3-d">{{item.ToAddr}}</div>
          </div>         
        </div>
        <div class="order-list-d3">
          <div style="width:100%;">
            <div style="margin:0 0 10px 0;">
              <div class="order-list-d3-d">狀態:{{GLOBAL.status_arr[item.Status]}}</div>
              <div class="order-list-d3-d">個案負擔 ${{item.SelfPayAmt+item.OtherAmt}}</div>
              <div class="order-list-d3-d">(車資 + 陪同金額)</div>
            </div>
            <div>
              <div class="order-list-d3-menu">
                <i class="el-icon-tickets" @click="to_more(item.Id)" title="檢視"></i>
                <i class="el-icon-s-promotion" @click="t_order(item)" title="轉單"></i>
                <i class="el-icon-delete-solid" @click="get_more(item)" title="取消訂單"></i>
              </div>
            </div>
          </div>         
        </div>
      </div>
    <div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	  <Page :max="list.pageCount" :url="'OrderList'" :q="seach"/>
  </div>
</template>

<script>
import Page from './Page'
import MBox from './MBox'
import moment from 'moment'
export default {
  data() {
    return {
    list:[],
    c_list:"",
    time:"",
    del_order:{
      point:"",
      remark:""
    },
    seach:this.Function.p_json(this.$route.query)
	}
  },
  components: {Page,MBox},
  mounted:async function() {
    this.GLOBAL.show_black=true
	  await this.all_ready()
    await this.get_data()
	  console.log(this.list)
	  this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
      //this.seach=to.query
      await this.get_data()
	    this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function() {
      if(this.seach.time===null) {
        this.seach.time=""
      }
	    this.list=await this.Function.get_OrderDetails(this.$route.params.page,this.seach.status,this.seach.pMode,encodeURI(this.seach.key),this.seach.time)
    },
    get_more:async function(x) {
      this.GLOBAL.show_black=true
      this.c_list=await this.Function.get_OrderDetails_more(x.Id)
      this.MBox.switch_open()
      this.GLOBAL.show_black=false
    },
    show_c:async function() {
      console.log(this.$route)
      console.log(this.seach)
      this.$router.push({ name:"OrderList" ,params:{page:1} ,query: this.Function.p_json(this.seach)})
    },
    to_more:function(x) {
      this.$router.push({ path: '/OrderDetails/'+x }) // -> /user/123
    },
    is_after:function(x) {
      return moment(x.ReservationDate).isAfter(moment())
    },
    to_del:async function() {
      let obj={
        "CaseUserId":this.c_list.CaseUser.Id,
        "OrderDetailId": this.c_list.OrderDetails.Id,
        "Point": this.del_order.point,
        "GrandCount": 0,
        "Remark": this.del_order.remark,
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.post_PostOrderViolation(obj))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    t_order:async function(x) {
      var r = confirm("確定轉單?");
      if(!r) {
        return 0
      }
      this.GLOBAL.show_black=true
      alert(await this.Function.put_PutDetailTransfer(x.Id))
      await this.get_data()
      this.GLOBAL.show_black=false
    }
  }
}
</script>



