<template>
  <div class="order">
    <h2>訂單詳細資訊</h2>
    <EditDespatchDetail v-if="show_edit" @closeEdit="show_edit = false;" :CaseUserId="list.CaseUser.Id" :OrderDetailsId="list.OrderDetails.Id" />
    <div class="order-header">
      <div class="order-header-d" :class="{'order-header-d-on':show===1}" @click="show=1">訂單明細</div>
      <div class="order-header-d" :class="{'order-header-d-on':show===2}" @click="show=2">基本資料</div>
      <div class="order-header-d" :class="{'order-header-d-on':show===3}" @click="show=3">可用補助</div>
    </div>
    <div style="overflow: hidden;clear: both;">
      <div class="order-case" v-if="list.CaseUser">
        <div class="order-case-d">個案編號:{{list.CaseUser.CaseUserNo}}</div>
        <div
          class="order-case-d"
          style="font-size:30px;"
        >{{list.CaseUser.Name}}({{list.CaseUser.UserMode ? "公費" : "自費"}})</div>
        <div class="order-case-d">身分證字號:{{list.CaseUser.UID}}</div>
        <div class="order-case-d">{{list.CaseUser.Staus ? "可派發" : "不可派發"}}</div>
        <img
          class="order-case-img"
          :src="list.CaseUser.Pic ? list.CaseUser.Pic:'https://api.donkeymove.com//UploadFolder/images/default.png'"
        />
        <div class="order-case-data">
          <div class="order-case-data-d">違規點數:{{list.TotalViolationCount}}</div>
          <div
            class="order-case-data-d"
          >停權到期日:{{list.CaseUser.ExpiredDate ? Function.moment.yyyymmdd(list.CaseUser.ExpiredDate) : "無"}}</div>
          <div
            class="order-case-data-d"
          >審核日期:{{list.CaseUser.ReviewDate ? Function.moment.yyyymmdd(list.CaseUser.ReviewDate) : "無"}}</div>
          <div class="order-case-data-d">失效日期:{{list.CaseUser.InvalidDate ? Function.moment.yyyymmdd(list.CaseUser.InvalidDate) : "無"}}</div>
          <div class="bt" @click="show_edit = true">修改訂單</div>
        </div>
      </div>
      <div class="order-d">
        <OrderDetailsMore :list="list" v-if="list.OrderDetails&&show===1" />
        <OrderDetailsCase :list="list" v-if="show===2" />
        <OrderDetailsSubsidy :list="list" :coup="coup" v-if="show===3" />
      </div>
    </div>
  </div>
</template>

<script>
import OrderDetailsMore from "./OrderDetailsMore";
import OrderDetailsCase from "./OrderDetailsCase";
import OrderDetailsSubsidy from "./OrderDetailsSubsidy";
import EditDespatchDetail from "./EditDespatchDetail";
export default {
  data() {
    return {
      list: [],
      coup: "",
      maps: [],
      show: 1,
      show_edit: false
    };
  },
  components: { OrderDetailsMore, OrderDetailsCase, OrderDetailsSubsidy,EditDespatchDetail },
  mounted: async function() {
    this.GLOBAL.show_black = true;
    await this.all_ready();
    await this.get_data();
    this.resize();
    window.addEventListener("resize", this.resize, false);
    this.GLOBAL.show_black = false;
    console.log(this.list);
  },
  watch: {
    async $route(to, from) {}
  },
  methods: {
    get_data: async function(x) {
      this.list = await this.Function.get_OrderDetails_more(
        this.$route.params.id
      );
      this.coup = await this.Function.get_case_Subsidy(this.list.CaseUser.Id);
      this.$nextTick(function() {
        var dom = document.querySelectorAll(".map");
        this.maps[0] = this.Function.initMap(dom[0]);
        this.Function.calculateAndDisplayRoute(
          this.maps[0],
          this.list.OrderDetails.FromAddr,
          this.list.OrderDetails.ToAddr
        );
      });
    },
    resize: function() {
      if (
        window.innerWidth >= 769 &&
        document.querySelectorAll(".order-case")[0]
      ) {
        document.querySelectorAll(".order-case")[0].style.height =
          document.querySelectorAll(".order-d")[0].offsetHeight + "px";
      } else {
        document.querySelectorAll(".order-case")[0].style.height = "auto";
      }
    }
  }
};
</script>



