<template>
  <div class="chart">
    <h2>車輛使用率比較表</h2>
    <div class="seach">
        <el-date-picker v-model="s_day" type="date" placeholder="選擇日期區間(起)" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
        <el-date-picker v-model="e_day" type="date" placeholder="選擇日期區間(迄)" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
	      <el-select v-model="c_county" placeholder="請選擇地區" @change="get_data">
          <el-option :label="'請選擇地區'" :value="0"></el-option>
          <el-option v-for="(item,index) in county" :label="item.District" :value="item.Id"></el-option>
	      </el-select>
	      <el-select v-model="c_comp" placeholder="請選擇公司" @change="get_data">
          <el-option :label="'請選擇公司'" :value="0"></el-option>
          <el-option v-for="(item,index) in comp" :label="item.CompanyName" :value="item.Id"></el-option>
	      </el-select>
    </div>
	<div class="chart-main">
    <p>單位(%)</p>
	  <canvas width="400" height="400" ref="c_main"></canvas>
	</div>
	<div class="chart-b" v-if="list&&list.length>0">
	  <div class="chart-d" v-for="(item,index) in list">
	    <div class="chart-d-h">{{item.CarNo}}</div>
	    <canvas class="chart-c1" :ref="'c'+index"></canvas>
	  </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	    list:[],
      ctxt:"",
      county:[],
      c_county:0,
      comp:[],
      c_comp:"",
      s_day:"",
      e_day:""
	  }
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
    await this.all_ready()
    this.county=await this.Function.get_GetTaiwanCounty_Id(189)
    this.comp=await this.Function.get_CarCompanys()
    this.c_comp=this.comp[1].Id
    await this.get_data()
	  this.GLOBAL.show_black=false
	  console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function(x) {
    this.GLOBAL.show_black=true
	  this.list=await this.Function.get_GetCarUseRate(this.s_day,this.e_day,this.c_comp,this.c_county).catch(e=>{alert("發生錯誤，請再試一次");this.GLOBAL.show_black=false})  
	  console.log(this.list)
	  this.$nextTick(function () {
	  var arr1=this.list.map(x=>x.CarNo)
    var arr2=this.list.map(x=>x.UseRate)
    var arr2_color=[]
      for(let i=0;i<this.list.length;i++) {
	    var obj=[
          {
            backgroundColor: [this.Function.r_color(),"#eee"],
            data: [this.list[i].UseRate,100-this.list[i].UseRate]
          }
        ]
        var ctx1 = this.$refs["c"+i]
        this.Function.chart.drew_chart(ctx1,'doughnut',["使用率","未使用率"],obj,true)
    }
    for(let i in arr2) {
      arr2_color.push("#444")
    }
	  var obj2=[
      {
		    label: '使用率',
        backgroundColor:arr2_color,
        data: arr2
      }
	  ]
      var ctx = this.$refs.c_main
      if(this.ctxt) {
        this.ctxt.destroy()
      }
      this.ctxt=this.Function.chart.drew_chart_remove_lab(ctx,'bar',arr1,obj2)
  })
    this.GLOBAL.show_black=false
	}
  }
}
</script>



