<template>
  <div class="mbox" v-if="open">
    <div class="mbox-black" @click="switch_open"></div>
    <div class="mbox-d">
      <div class="mbox-d-h">
        {{mode=="GetPickMission" ? "任務明細檔" : mode=="GetServiceRecord" ? "服務紀錄" : "其他"}}
        <i class="el-icon-close mbox-d-h-x" @click="switch_open"></i>
      </div>
      <div class="mbox-d-c">
        <div v-if="mode=='GetPickMission'">
          <el-date-picker v-model="time1" value-format="yyyy-MM-dd" type="date" placeholder="請輸入開始日期"></el-date-picker>
          <el-date-picker v-model="time2" value-format="yyyy-MM-dd" type="date" placeholder="請輸入結束日期"></el-date-picker>
          <downloadExcel :fetch="sub" type="xls" name="任務明細檔.xls"><div class="bt" @click="sub()">確定提交</div></downloadExcel>
        </div>
        <div v-if="mode=='GetServiceRecord'">
          <el-date-picker v-model="time1" value-format="yyyy-MM-dd" type="date" placeholder="請輸入開始日期"></el-date-picker>
          <el-date-picker v-model="time2" value-format="yyyy-MM-dd" type="date" placeholder="請輸入結束日期"></el-date-picker>
	        <el-row>
            <el-col :span="8">公司</el-col>
            <el-col :span="16">
              <el-select v-model="c_list.comp" placeholder="請選擇公司">
                <el-option v-for="(item,index) in list.comp" :key="index" :label="item.CompanyName" :value="item.Id"></el-option>
	            </el-select>           
            </el-col>
          </el-row>
	        <el-row>
            <el-col :span="8">性別</el-col>
            <el-col :span="16">
	            <el-select v-model="c_list.sex" placeholder="請選擇性別">
                <el-option v-for="(item,index) in list.sex" :key="index" :label="item.title" :value="item.value"></el-option>
	            </el-select>        
            </el-col>
          </el-row>
	        <el-row>
            <el-col :span="8">失能等級</el-col>
            <el-col :span="16">
	            <el-select v-model="c_list.dis" placeholder="請選擇失能等級">
                <el-option v-for="(item,index) in list.dis.data" :key="index" :label="item.TypeName" :value="item.Id"></el-option>
	            </el-select>        
            </el-col>
          </el-row>
	        <el-row>
            <el-col :span="8">社會福利身分</el-col>
            <el-col :span="16">
              <el-select v-model="c_list.utype" placeholder="請選擇社會福利身分">
                <el-option v-for="(item,index) in list.utype.data" :key="index" :label="item.TypeName" :value="item.Id"></el-option>
	            </el-select>           
            </el-col>
          </el-row>
          <downloadExcel :fetch="sub" type="xls" name="服務紀錄檔.xls"><div class="bt" @click="sub()">確定提交</div></downloadExcel>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import JsonExcel from 'vue-json-excel'
Vue.component('downloadExcel', JsonExcel)
export default {
  data() {
    return {
      open:false,
      time1:"",
      time2:"",
      mode:"",
      list:{
        comp:[],
        sex:[{title:"男",value:1},{title:"女",value:2}],
        utype:[],
        dis:[]
      },
      c_list:{
        comp:"",
        sex:"",
        utype:"",
        dis:""
      }
	}
  },
  mounted :async function() {
    Vue.prototype.ExportTable=this
    this.re_size()
  },
  methods:{
    get_data:async function() {
      this.list.comp=await this.Function.get_CarCompanys()
      this.list.utype=await this.Function.get_parentID(1)
      this.list.dis=await this.Function.get_parentID(3)
    },
    re_size:async function() {
      var dom=document.querySelectorAll(".mbox-d")[0]
      if (!dom) {
        return 0
      }
      var w=dom.offsetWidth
      var h=dom.offsetHeight
      dom.style.margin=-(h/2)+"px 0 0 "+-(w/2)+"px"
      console.log(dom)
    },
    switch_open:async function(x) {
      this.mode=x
      if(x=="GetServiceRecord") {
        this.GLOBAL.show_black=true
        await this.get_data()
        this.GLOBAL.show_black=false
      }
      this.$nextTick(async function () {
        this.open=!this.open
        await this.Function.delay(0)
        this.re_size()
      })
    },
    sub:async function() {
      var obj=""
      this.GLOBAL.show_black=true
      if(this.mode=="GetPickMission") {
        obj=await this.Function.export_GetPickMission(this.time1,this.time2)
      }
      else if(this.mode=="GetServiceRecord") {
        obj=await this.Function.export_GetServiceRecord(this.time1,this.time2,this.c_list.utype,this.c_list.dis,this.c_list.sex,this.c_list.comp)
      }
      //this.Function.export.convertAndExport(obj)
      this.GLOBAL.show_black=false
      return obj
    }
  }
}
</script>


