<template>
  <div class="dispatch">
    <h2>調度控制台-表格式</h2>
    <MBox :title="'訂單詳細資料'" ref="mb1">
      <div class="dispatch-mbox">
		  <div>
		  <h3>訂單資料</h3>
        <div v-if="!isd">
          <el-button @click="show_c_time=!show_c_time">變更時間</el-button>
          <div v-if="show_c_time" style="padding: 10px 0;">
            <el-select placeholder="請選擇時間" v-model="c_time" style="width:78%;">
              <el-option v-for="(item,index) in time" :label="item" :value="item"></el-option>
            </el-select>
            <el-button style="width:20%;" @click="set_time()">確定</el-button>
          </div>
        </div>
        <div class="more-d" v-if="!isd"><span>訂單編號: {{show_order.SOrderNo}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>訂單編號: {{show_order.OrderDetails.SOrderNo}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>預約搭車時間: {{Function.moment.hhmm(show_order.ReservationDate)}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>預約搭車時間: {{Function.moment.hhmm(show_order.OrderDetails.ReservationDate)}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>是否回程: {{show_order.IsBack ? "是":"否"}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>是否回程: {{show_order.OrderDetails.IsBack ? "是":"否"}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>共乘: {{show_order.CanShared ? "是":"否"}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>共乘: {{show_order.OrderDetails.CanShared ? "是":"否"}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>輪椅類型: {{show_order.WheelchairId}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>輪椅類型: {{show_order.OrderDetails.WheelchairId}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>陪同人數: {{show_order.FamilyWith}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>陪同人數: {{show_order.OrderDetails.FamilyWith}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>起點: {{show_order.FromAddr}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>起點: {{show_order.OrderDetails.FromAddr}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>迄點: {{show_order.ToAddr}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>迄點: {{show_order.OrderDetails.ToAddr}}</span><hr></div>
	    </div>
		<div>
		  <h3>個案資料</h3>
		    <div class="more-d" v-if="!isd"><span>個案編號: {{show_order.CaseUserNo}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>個案編號: {{show_order.CaseUser.CaseUserNo}}</span><hr></div>
        <div class="more-d" v-if="!isd"><span>個案姓名: {{show_order.CaseUserName}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>個案姓名: {{show_order.CaseUser.Name}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>市話: {{show_order.CaseUserTel}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>市話: {{show_order.CaseUser.Tel}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>手機: {{show_order.CaseUserPhone}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>手機: {{show_order.CaseUser.Phone}}</span><hr></div>
		    <div class="more-d" v-if="!isd"><span>身分證字號: {{show_order.CaseUserUID}}</span><hr></div>
        <div class="more-d" v-if="isd"><span>身分證字號: {{show_order.CaseUser.UID}}</span><hr></div>
	    </div>
	  </div>
	</MBox>
    <div class="seach">
        <el-date-picker v-model="day" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd" @change="get_data"></el-date-picker>
        <el-button type="primary" icon="el-icon-document-checked" @click="put_data()">推播</el-button>
    </div>
    <div class="dispatch-main">
      <div class="dispatch-order" ref="order_d">
        <div class="dispatch-order-d" v-for="(item,index) in order" @dblclick="open_mbox(false,item)">
          <div>{{item.SOrderNo}} ({{item.CarCategoryId==236 ? "一般車":"福祉車"}})</div>
          <div>{{item.CaseUserName}} ({{item.SubsidyMode ? "公費":"自費"}})</div>
          <div>起: {{item.FromAddr}}</div>
          <div>迄: {{item.ToAddr}}</div>
          <div>{{Function.moment.hhmm(item.ReservationDate)+" / 預估"+item.ExpectedMinute+"分鐘"}}</div>
          <div>{{item.CanShared ? "可共乘":"不可共乘"}}</div>
          <el-checkbox v-model="c_order" :label="item" v-if="!check_order||c_order[0]==item" @click.stop="">勾選</el-checkbox>
          <el-checkbox v-model="c_order" :label="item" v-if="check_order&&item.CanShared&&c_order[0].CanShared&&c_order[0]!=item&&item.CarCategoryId==c_order[0].CarCategoryId">共乘</el-checkbox>
        </div>
      </div>
      <div class="dispatch-car">
        <div class="dispatch-car-d" v-for="(item,index) in car" v-if="item.DriverInfo" @click="item.open=!item.open;$forceUpdate();">
          <div class="dispatch-car-d-d">{{item.CarNo}} ({{item.DriverInfo.DriverName}})
            <div style="float:right;">
              <span>座位:{{item.SeatNum}} | </span>
              <span>輪椅數:{{item.WheelchairNum}} | </span>
              <span v-if="item.CarCategory&&item.CarCategory[0]">{{item.CarCategory[0].CarTypeName}}</span>
              <el-button type="primary" icon="el-icon-circle-plus-outline" @click="sub(item)" v-if="check_order&&check_car(item)">排到此司機</el-button>
            </div>
          </div>
          <div class="dispatch-car-d-dis">
            <div class="dispatch-car-d-dis-d" v-for="(item2,index2) in get_dis_by_driverid(item)" @click.stop="">
              <div>
                {{item2.DespatchDetails[0].Despatch.MDespatchNo}}{{item2.DespatchDetails.length==1 ? "":" (已共乘)"}}
                <ol>
                  <li v-for="(item3,index3) in item2.DespatchDetails" @dblclick="open_mbox(true,item3)" style="overflow: hidden;">
                    <div style="width:40%;float:left;word-break: break-all;">{{index3+1+". "+item3.OrderDetails.SOrderNo}}({{item3.CaseUser.Name}})</div>
                    <div style="width:40%;float:left;word-break: break-all;">{{item3.OrderDetails.ToAddr}}</div>
                    <div style="width:20%;float:left;word-break: break-all;">{{Function.moment.hhmm(item3.OrderDetails.ReservationDate)+" / 預估"+item3.OrderDetails.ExpectedMinute+"分鐘"}}</div>
                    <i class="el-icon-close dispatch-car-d-dis-d-x" @click="del_des(item3)"></i>
                  </li>
                </ol>
              </div>
            </div>
          </div>
          <el-collapse-transition>
            <div v-if="item.open" @click.stop="">
              <DriverStroke :order="get_dis_by_driverid(item)" :mbox="open_mbox" />
            </div>
          </el-collapse-transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import MBox from './MBox'
import DriverStroke from './DriverStroke'
export default {
  data() {
    return {
	    order:"",
	    dispatch:"",
      car:"",
      c_order:[],
      show_order:"",
      check_order:false,
      isd:false,
      show_c_time:false,
      c_time:"",
      time:this.GLOBAL.time_list, 
      day:moment().format("YYYY-MM-DD")
	  }
  },
  components: {DriverStroke,MBox},
  mounted:async function() {
    var orderd=document.querySelectorAll(".dispatch-order")[0]
    var orderdt=orderd.offsetTop
    this.GLOBAL.show_black=true
	await this.all_ready()
  await this.get_data()
  document.querySelectorAll('.d1-r')[0].addEventListener("scroll",function(e) {
    if(e.target.scrollTop>=200) {
      orderd.style.top=e.target.scrollTop-orderdt+"px"
    }
    else {
      orderd.style.top="0px"
    }
  }, false);
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
	  await this.get_data()
	  this.GLOBAL.show_black=false
    },
    c_order:function() {
      console.log(this.c_order)
      if(this.c_order.length==0) {
        this.check_order=false
      }
      else {
        this.check_order=true
      }
    }
  },
  methods:{
    get_data:async function(x) {
      this.GLOBAL.show_black=true
      this.order=await this.Function.get_dispatch_order(this.day)
	    this.dispatch=await this.Function.get_dispatch_allgroup(this.day,"")
	    this.car=await this.Function.get_dispatch_car("")
	    for(let i in this.dispatch) {
	      this.dispatch[i].time=moment(this.dispatch[i].DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm")
      }
	    for(let i in this.car) {
	      this.car[i].open=false
	    }
      //console.log(this.order)
      //console.log(this.car)
      //console.log(this.dispatch)
      this.c_order=[]
      this.GLOBAL.show_black=false
    },
    open_mbox:function(x,y) {
      this.isd=x;
      this.show_order=y;
      this.MBox.switch_open()
    },
    get_dis_by_driverid:function(x) {
      var arr=[]
      for(let i in this.dispatch) {
        if(x.DriverId==this.dispatch[i].DespatchDetails[0].Despatch.DriverInfoId) {
          arr.push(this.dispatch[i])
        }
      }
      return arr
    },
    post_des:async function(x,order) {  //新增調度單
			var obj = {
				"DriverInfoId": x.DriverInfo.Id,
				"CarId": x.Id,
				"StartDate": moment(order.ReservationDate).format("YYYY-MM-DD HH:mm"),
				"DespatchDetail": [
					{ "OrderDetailId": order.Id }
				]
      }
      var data=await this.Function.post_Despatch(obj)
      return data
    },
    del_des:async function(x) {
      console.log(x)
      this.GLOBAL.show_black=true
      this.$message(await this.Function.del_Despatch(x.OrderDetails.Id))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    with_order:async function(x,order) {  //共乘
      var obj = {
				"Id": x,
				"DriverInfoId": 0,
				"CarId": 0,
				"OrderDetailId": order.Id
      }
      var data=await this.Function.with_Despatch(obj)
      return data
    },
    sub:async function(x) {
      this.GLOBAL.show_black=true
      var flag=await this.post_des(x,this.c_order[0])
      if(this.c_order.length>1) {
        for(let i=1;i<this.c_order.length;i++) {
          await this.with_order(flag,this.c_order[i])
        }
      }
      this.c_order=[]
      this.check_order=false
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    put_data:async function() {
      this.GLOBAL.show_black=true
      this.$message(await this.Function.put_Despatch(this.day))
      await this.get_data()
      this.GLOBAL.show_black=false
    },
    check_car:function(x) {
      if(this.c_order[0].CarCategoryId==236) {
        return true
      }
      else if(x.CarCategory[0].Id==214&&this.c_order[0].CarCategoryId==214) {
        return true
      }
      else {
        return false
      }
    },
    set_time:async function() {
      var time=this.day+" "+this.c_time
      this.GLOBAL.show_black=true
      alert(await this.Function.post_PutDetailTime(this.show_order.Id,time))
      await this.get_data()
      this.MBox.switch_open()
      this.GLOBAL.show_black=false
    }
  }
}
</script>


