<template>
  <div>
    <h3>基本資料</h3>
    <el-row><el-col :span="4">市話</el-col><el-col :span="20">{{list.CaseUser.Tel}}</el-col></el-row>
    <el-row><el-col :span="4">手機</el-col><el-col :span="20">{{list.CaseUser.Phone}}</el-col></el-row>
    <el-row><el-col :span="4">生日</el-col><el-col :span="20">{{Function.moment.yyyymmdd(list.CaseUser.Birthday)}}</el-col></el-row>
    <el-row><el-col :span="4">Email</el-col><el-col :span="20">{{list.CaseUser.Email}}</el-col></el-row>
    <el-row><el-col :span="4">居住地址</el-col><el-col :span="20">{{list.CaseUser.CensusCounty+" "+list.CaseUser.CensusDistrict+" "+list.CaseUser.CensusAddr}}</el-col></el-row>
    <el-row><el-col :span="4">通訊地址</el-col><el-col :span="20">{{list.CaseUser.CommCounty+" "+list.CaseUser.CommDistrict+" "+list.CaseUser.CommAddr}}</el-col></el-row>
    <el-row><el-col :span="4">聯絡人姓名</el-col><el-col :span="20">{{list.CaseUser.UrgentName}}</el-col></el-row>
    <el-row><el-col :span="4">聯絡人關係</el-col><el-col :span="20">{{list.CaseUser.UrgentRelationship}}</el-col></el-row>
    <el-row><el-col :span="4">聯絡人市話</el-col><el-col :span="20">{{list.CaseUser.UrgentTel}}</el-col></el-row>
    <el-row><el-col :span="4">聯絡人手機</el-col><el-col :span="20">{{list.CaseUser.UrgentPhone}}</el-col></el-row>
    <el-row><el-col :span="4">聯絡人Email</el-col><el-col :span="20">{{list.CaseUser.UrgentEmail}}</el-col></el-row>
  </div>
</template>

<script>
export default {
  props:["list"],
  data() {
    return {
	  }
  },
  mounted:async function() {
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
  }
}
</script>



